<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered modal-lg">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">{{ $t('BulkMove') }}</h5>
                            <button type="button" class="close" @click="onClose">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <div class="row">
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('ClientName') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.ClientId"
                                            @change="onClientChange"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in clientOptions"
                                                :key="item.value"
                                                :value="item.value"
                                            >
                                                {{ item.text }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('UtilityName') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.UtilityName"
                                            @change="onUtilityChange"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in utilityOptions"
                                                :key="item.value"
                                                :value="item.value"
                                            >
                                                {{ item.name }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('sba_id') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.SbaId"
                                            @change="onSbaChange"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in sbaOptions"
                                                :key="item.value"
                                                :value="item.value"
                                            >
                                                {{ item.name }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('Division') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.DivisionId"
                                            @change="onDivisionChange"
                                        >
                                            <option value="">{{ $t('SelectDivision') }}</option>
                                            <option
                                                v-for="item in divisionOptions"
                                                :key="item.value"
                                                :value="item.value"
                                            >
                                                {{ item.name }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('InvoiceNumber') }}</label>
                                        <select
                                            class="form-control"
                                            v-model="form.InvoiceNumber"
                                        >
                                            <option value="">{{ $t('Select') }} {{ $t('InvoiceNumber') }}</option>
                                            <option
                                                v-for="item in invoiceOptions"
                                                :key="item.value"
                                                :value="item.value"
                                            >
                                                {{ item.name }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="modal-footer">
                            <button class="btn btn-success" @click="onSave">
                                <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                            </button>
                            <button class="btn btn-danger" @click="onClose">
                                <em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { locateBillingService } from '../../../services/locateBillingService';

export default {
    name: 'ChangeClientAndUtilityInvoice',
    props: {
        lineItemIds: {
            type: [String, Array],
            default: ''
        },
        oldClientId: {
            type: [Number, String],
            default: null
        },
        oldUtilityName: {
            type: String,
            default: ''
        },
        workDateFrom: {
            type: String,
            default: ''
        },
        workDateTo: {
            type: String,
            default: ''
        },
        requestFrom: {
            type: String,
            default: 'lineitem'
        },
        allInvoice: {
            type: String,
            default: ''
        },
        xmlData: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            isLoading: false,
            clientOptions: [],
            utilityOptions: [],
            sbaOptions: [],
            divisionOptions: [],
            invoiceOptions: [],
            form: {
                ClientId: '',
                UtilityName: '',
                SbaId: '',
                DivisionId: '',
                InvoiceNumber: ''
            }
        };
    },
    created() {
        this.loadClients();
    },
    methods: {
        loadClients() {
            this.isLoading = true;
            locateBillingService.getClientList({ reqFrom: 'CUSTOM' })
                .then(response => {
                    this.isLoading = false;
                    if (response.data && response.data.items) {
                        this.clientOptions = response.data.items.map(item => ({
                            text: item.text || item.CHILD_NAME,
                            value: item.value || item.CHILD_USER_ID
                        }));
                    }
                })
                .catch(() => {
                    this.isLoading = false;
                });
        },
        onClientChange() {
            this.form.UtilityName = '';
            this.form.SbaId = '';
            this.form.DivisionId = '';
            this.form.InvoiceNumber = '';
            this.utilityOptions = [];
            this.sbaOptions = [];
            this.divisionOptions = [];
            this.invoiceOptions = [];
            if (!this.form.ClientId) return;

            this.isLoading = true;
            const url = `clientId=${this.form.ClientId}&configId=&reqFrom=&clientIds=`;
            locateBillingService.getUtilityList(url)
                .then(response => {
                    this.isLoading = false;
                    if (response.data) {
                        this.utilityOptions = response.data.map(u => ({
                            name: u.UTILITY_NAME,
                            value: u.UTILITY_NAME
                        }));
                    }
                })
                .catch(() => {
                    this.isLoading = false;
                });
        },
        onUtilityChange() {
            this.form.SbaId = '';
            this.form.DivisionId = '';
            this.form.InvoiceNumber = '';
            this.sbaOptions = [];
            this.divisionOptions = [];
            this.invoiceOptions = [];
            if (!this.form.UtilityName) return;

            // Load SBA list for selected utility
            const url = `UtilityId=${this.form.UtilityName}`;
            locateBillingService.GetSbaIdListByUtilityId(url)
                .then(response => {
                    if (response.data) {
                        this.sbaOptions = response.data.map(s => ({
                            name: s.NAME,
                            value: s.SBA_ID
                        }));
                    }
                })
                .catch(() => {});
        },
        onSbaChange() {
            this.form.DivisionId = '';
            this.form.InvoiceNumber = '';
            this.divisionOptions = [];
            this.invoiceOptions = [];

            if (!this.form.SbaId || !this.form.ClientId || !this.form.UtilityName) return;

            // Load divisions by client + utility
            const url = `clientId=${this.form.ClientId}&utilityIds=${this.form.UtilityName}`;
            locateBillingService.LoadClientDivisionListByUtilityIds(url)
                .then(response => {
                    if (response.data) {
                        this.divisionOptions = response.data.map(d => ({
                            name: d.NAME,
                            value: d.VALUE
                        }));
                    }
                })
                .catch(() => {});
        },
        onDivisionChange() {
            this.form.InvoiceNumber = '';
            this.invoiceOptions = [];
            if (!this.form.DivisionId || !this.form.ClientId) return;

            const url = `clientId=${this.form.ClientId}&divisionId=${this.form.DivisionId}&type=division`;
            locateBillingService.LoadInvoiceNumberList(url)
                .then(response => {
                    if (response.data) {
                        this.invoiceOptions = response.data.map(i => ({
                            name: i.NAME,
                            value: i.BILL_ID
                        }));
                    }
                })
                .catch(() => {});
        },
        onSave() {
            // Basic validation
            if (!this.form.ClientId || !this.form.UtilityName || !this.form.SbaId || !this.form.DivisionId) {
                // TODO: Hook into global validation / alert
                return;
            }

            // Optional same-utility/SBA/invoice validation
            const readyToGo = true;
            if (!readyToGo) {
                return;
            }

            const postData = {
                utilityName: this.form.UtilityName,
                clientId: this.form.ClientId,
                sbaId: this.getSbaText(this.form.SbaId),
                beSbaId: this.form.SbaId,
                divisionId: this.form.DivisionId,
                lineItemIds: Array.isArray(this.lineItemIds) ? this.lineItemIds.join(',') : this.lineItemIds,
                reqFrom: this.requestFrom,
                ticketNumber: '', // not used in this Vue version
                activityId: '',
                oldClient: this.oldClientId,
                oldUtility: this.oldUtilityName,
                workDateFrom: this.workDateFrom,
                workDateTo: this.workDateTo,
                billId: this.form.InvoiceNumber,
                allInvoice: this.allInvoice,
                xmlData: this.xmlData,
                bulkMoveBatchId: '' // could be extended later
            };

            this.isLoading = true;
            locateBillingService.UpdateContractAndUtility(postData)
                .then(response => {
                    this.isLoading = false;
                    if (response.data && response.data.flag === 'success') {
                        this.$emit('saved');
                        this.onClose();
                    } else {
                        // TODO: surface error message
                    }
                })
                .catch(() => {
                    this.isLoading = false;
                });
        },
        getSbaText(sbaId) {
            const opt = this.sbaOptions.find(x => x.value === sbaId);
            return opt ? opt.name : '';
        },
        onClose() {
            this.$emit('close');
        }
    }
};
</script>

<style scoped>
.mandatory {
    color: red;
}
</style>


