<template>
    <section>
        <div class="my-popups">
            <Loader :is-visible="isLoading" />
            <div class="modal d-block" style="z-index: 1050;">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">{{ $t('Discount') }}</h5>
                            <button type="button" class="close text-white" @click="onCancel">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <span class="user-guide">
                                    <a 
                                        class="ancuserguide" 
                                        data-toggle="collapse" 
                                        href="#collapseReplyugdisc" 
                                        role="button" 
                                        aria-expanded="false" 
                                        aria-controls="collapseExample"
                                    >
                                        {{ $t('UserGuide') }}
                                    </a>
                                    <div id="collapseReplyugdisc" class="divancuserguide collapse">
                                        <div id="scrollbarreplyuserguide">
                                            <div class="con">
                                                <div v-html="$t('User_Guide_Billing_Rule_Discount_Amount')"></div>
                                            </div>
                                        </div>
                                    </div>
                                </span>
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{ $t('DiscountAmount') }}:<span class="mandatory">*</span></label>
                                        <input 
                                            type="text" 
                                            id="txtAddDiscount" 
                                            v-model="discountData.discountAmount" 
                                            maxlength="15" 
                                            @keydown="onKeyDown"
                                            name="txtAddDiscount" 
                                            class="form-control"
                                            :class="{ 'is-invalid': errors.discountAmount }"
                                        />
                                        <span v-if="errors.discountAmount" class="invalid-feedback">{{ errors.discountAmount }}</span>
                                    </div>
                                </div>
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{ $t('Description') }}:<span class="mandatory">*</span></label>
                                        <textarea 
                                            id="txtAddDescription" 
                                            class="form-control" 
                                            name="txtAddDescription" 
                                            maxlength="500"
                                            v-model="discountData.description"
                                            :class="{ 'is-invalid': errors.description }"
                                        ></textarea>
                                        <span v-if="errors.description" class="invalid-feedback">{{ errors.description }}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <div class="row flex-row-reverse w-100 mt-2">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success" @click="onSave" :title="$t('Save')">
                                        <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                    </a>
                                    <a class="btn btn-danger" href="javascript:;" @click="onCancel" :title="$t('Cancel')">
                                        <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                    </a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2">
                                        <span class="mandatory">*</span> {{ $t('MandatoryField') }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { locateBillingService } from '../../../services/locateBillingService';
import Loader from '../../../components/Common/Loader.vue';

export default {
    components: {
        Loader
    },
    name: "AddDiscount",
    props: {
        itemId: {
            type: Number,
            required: true
        },
        itemCost: {
            type: Number,
            default: 0
        }
    },
    data() {
        return {
            isLoading: false,
            discountData: {
                discountAmount: '',
                description: ''
            },
            errors: {
                discountAmount: '',
                description: ''
            }
        }
    },
    async created() {
        await this.loadDiscount();
    },
    watch: {
        itemId: {
            handler: function(newVal, oldVal) {
                if (newVal && newVal !== oldVal) {
                    this.loadDiscount();
                }
            },
            immediate: false
        }
    },
    methods: {
        async loadDiscount() {
            var vm = this;
            vm.isLoading = true;
            try {
                var url = `itemId=${vm.itemId}`;
                var response = await locateBillingService.GetDiscount(url);
                if (response && response.data) {
                    var discountAmount = response.data.discountAmount;
                    if (discountAmount !== null && discountAmount !== undefined) {
                        vm.discountData.discountAmount = parseFloat(discountAmount).toFixed(2);
                    } else {
                        vm.discountData.discountAmount = '';
                    }
                    vm.discountData.description = response.data.description || '';
                } else {
                    // No existing discount, clear fields
                    vm.discountData.discountAmount = '';
                    vm.discountData.description = '';
                }
            } catch (error) {
                console.log('Error loading discount:', error);
                // On error, clear fields
                vm.discountData.discountAmount = '';
                vm.discountData.description = '';
            } finally {
                vm.isLoading = false;
            }
        },
        onKeyDown(event) {
            // Allow numbers, dot, backspace, delete, tab, escape, enter
            var allowedKeys = [8, 9, 27, 13, 46, 110, 190];
            // Allow Ctrl+A, Ctrl+C, Ctrl+V, Ctrl+X
            if ((event.ctrlKey || event.metaKey) && [65, 67, 86, 88].indexOf(event.keyCode) !== -1) {
                return true;
            }
            // Allow numbers
            if ((event.keyCode >= 48 && event.keyCode <= 57) || (event.keyCode >= 96 && event.keyCode <= 105)) {
                return true;
            }
            // Allow dot
            if (allowedKeys.indexOf(event.keyCode) !== -1) {
                return true;
            }
            event.preventDefault();
            return false;
        },
        validate() {
            var vm = this;
            vm.errors = {
                discountAmount: '',
                description: ''
            };
            var hasError = false;

            if (!vm.discountData.discountAmount || vm.discountData.discountAmount.trim() === '') {
                vm.errors.discountAmount = vm.$t('Required').replace('{0}', vm.$t('Amount'));
                hasError = true;
            } else {
                var discountValue = parseFloat(vm.discountData.discountAmount);
                if (isNaN(discountValue) || discountValue < 0) {
                    vm.errors.discountAmount = vm.$t('InvalidAmount');
                    hasError = true;
                } else if (discountValue > vm.itemCost) {
                    vm.errors.discountAmount = vm.$t('DISCOUNTMORETHENZEROMESSAGE').replace('{0}', vm.itemCost.toFixed(2));
                    hasError = true;
                }
            }

            if (!vm.discountData.description || vm.discountData.description.trim() === '') {
                vm.errors.description = vm.$t('Required').replace('{0}', vm.$t('Description'));
                hasError = true;
            }

            return !hasError;
        },
        async onSave() {
            var vm = this;
            if (!vm.validate()) {
                return;
            }

            var discountValue = parseFloat(vm.discountData.discountAmount);
            if (discountValue > vm.itemCost) {
                vm.ShowAlert(vm.$t('DISCOUNTMORETHENZEROMESSAGE').replace('{0}', vm.itemCost.toFixed(2)), "warning", vm.$t('Alert'));
                return;
            }

            vm.isLoading = true;
            vm.$emit('saving');
            try {
                var requestData = {
                    ItemId: vm.itemId,
                    Discount: discountValue,
                    Description: vm.discountData.description.trim()
                };

                var response = await locateBillingService.AddDiscount(requestData);
                if (response && response.data) {
                    var status = response.data.status;
                    var message = status === 0 
                        ? vm.$t('AddedSuccess').replace('{0}', vm.$t('USICDiscount'))
                        : vm.$t('UpdatedSuccess').replace('{0}', vm.$t('USICDiscount'));
                    
                    vm.$emit('saved', {
                        itemId: vm.itemId,
                        discount: discountValue.toFixed(2),
                        status: status
                    });
                    
                    vm.ShowAlert(message, "success", vm.$t('Alert'));
                } else {
                    vm.ShowAlert(vm.$t('AddedError').replace('{0}', vm.$t('Discount')), "failure", vm.$t('Alert'));
                }
            } catch (error) {
                console.log('Error saving discount:', error);
                vm.ShowAlert(vm.$t('ErrorWhileProcessingRequest'), "failure", vm.$t('Alert'));
            } finally {
                vm.isLoading = false;
                vm.$emit('saved-complete');
            }
        },
        onCancel() {
            this.$emit('cancel');
        }
    }
}
</script>

<style scoped>
.is-invalid {
    border-color: #dc3545;
}
.invalid-feedback {
    display: block;
    width: 100%;
    margin-top: 0.25rem;
    font-size: 0.875rem;
    color: #dc3545;
}
</style>


