<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block" style="z-index: 1050;">
                <div class="modal-dialog modal-dialog-centered modal-xl">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">
                                {{ $t('Advance') }} {{ $t('Filter') }}
                            </h5>
                            <button type="button" class="close text-white" @click="onClose">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body" style="max-height: 70vh; overflow-y: auto;">
                            <div class="listing">
                                <div class="table-responsive overflowauto">
                                    <table id="tblAdvanceFilter" class="table table-bordered" style="min-width:600px;">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th width="5%">{{ $t('SerialNumbering') }}</th>
                                                <th width="10%"><span>{{ $t('RelationWithBelowColumn') }}</span></th>
                                                <th width="5%">{{ $t('OpeningBracket') }}</th>
                                                <th width="20%">{{ $t('fieldName') }}</th>
                                                <th width="23%">{{ $t('ConditionalOperator') }}</th>
                                                <th class="nonresize" data-width="350" style="width: 350px; min-width: 350px; max-width:350px;">{{ $t('Value') }}</th>
                                                <th width="8%">{{ $t('ClosingBracket') }}</th>
                                                <th width="5%"></th>
                                            </tr>
                                        </thead>
                                        <tbody id="advanceFilterBody">
                                            <tr 
                                                v-for="(row, index) in filterRows" 
                                    :key="index"
                                                id="trFirst"
                                            >
                                                <td class="up text-center">
                                                    {{ index + 1 }}
                                                </td>
                                                <td class="up overflow-visible align-top">
                                                    <select 
                                                        :id="'Relation_' + index"
                                                        :name="'Relation_' + index"
                                                        v-model="row.relationWithParent"
                                                        class="form-control width_88 ddlRelation"
                                                        :style="{ display: index === 0 ? 'none' : 'block' }"
                                                    >
                                                        <option value="">{{ $t('Select') }}</option>
                                                        <option 
                                                            v-for="rel in relationList" 
                                                            :key="rel.Value" 
                                                            :value="rel.Value"
                                                        >
                                                            {{ rel.Text }}
                                                        </option>
                                                    </select>
                                                </td>
                                                <td class="up overflow-visible align-top">
                                            <input
                                                type="text"
                                                        :id="'OpeningBrackets_' + index"
                                                        :name="'OpeningBrackets_' + index"
                                                        v-model="row.openingBrackets"
                                                        class="form-control txtOpeningBrackets"
                                                        maxlength="5"
                                                    />
                                                </td>
                                                <td class="up overflow-visible align-top">
                                                    <select 
                                                        :id="'TemplateId_' + index"
                                                        :name="'TemplateId_' + index"
                                                        v-model="row.templateId"
                                                        class="form-control w-100 ddlField"
                                                        @change="onFieldChange(index)"
                                                    >
                                                        <option value="">{{ $t('Select') }}</option>
                                                        <option 
                                                            v-for="field in filterFields" 
                                                            :key="field.TEMPLATE_ID" 
                                                            :value="field.TEMPLATE_ID"
                                                            :data-type="field.DATA_TYPE"
                                                            :data-columnname="field.COLUMN_NAME"
                                                            :data-tablename="field.TABLE_NAME"
                                                        >
                                                            {{ field.DISPLAY_NAME }}
                                                        </option>
                                                    </select>
                                                </td>
                                                <td class="up overflow-visible align-top">
                                                    <select 
                                                        :id="'OperatorId_' + index"
                                                        :name="'OperatorId_' + index"
                                                        v-model="row.operatorId"
                                                        class="form-control ddlOperator w-100 marginbottom5"
                                                        @change="onOperatorChange(index)"
                                                    >
                                                        <option value="">{{ $t('Select') }}</option>
                                                        <option 
                                                            v-for="op in row.operatorList" 
                                                            :key="op.OPERATOR_ID" 
                                                            :value="op.OPERATOR_ID"
                                                            :data-expression="op.OPERATOR_EXPRESSION"
                                                            :data-datatype="op.OPERATION_DATA_TYPE"
                                                        >
                                                            {{ op.OPERATOR_DISPLAY_NAME }}
                                                        </option>
                                                    </select>
                                                    
                                                    <!-- Additional Template and Operator for special operators -->
                                                    <select 
                                                        v-if="showAdditionalFields(row)"
                                                        :id="'AdditionalTemplateId_' + index"
                                                        :name="'AdditionalTemplateId_' + index"
                                                        v-model="row.additionalTemplateId"
                                                        class="form-control w-48 float-left mt-2 ddlAdditionalTemplateId"
                                                        @change="onAdditionalFieldChange(index)"
                                                    >
                                                        <option value="">{{ $t('Select') }}</option>
                                                        <option 
                                                            v-for="field in getAdditionalFieldList(row)" 
                                                            :key="field.TEMPLATE_ID" 
                                                            :value="field.TEMPLATE_ID"
                                                        >
                                                            {{ field.DISPLAY_NAME }}
                                                        </option>
                                                    </select>
                                                    
                                                    <select 
                                                        v-if="showAdditionalFields(row)"
                                                        :id="'AdditionalOperatorId_' + index"
                                                        :name="'AdditionalOperatorId_' + index"
                                                        v-model="row.additionalOperatorId"
                                                        class="form-control w-48 float-right mt-2 ddlAdditionalOperatorId"
                                                    >
                                                        <option value="">{{ $t('Select') }}</option>
                                                        <option 
                                                            v-for="op in getAdditionalOperatorList(row)" 
                                                            :key="op.OPERATOR_ID" 
                                                            :value="op.OPERATOR_ID"
                                                            :data-expression="op.OPERATOR_EXPRESSION"
                                                        >
                                                            {{ op.OPERATOR_DISPLAY_NAME }}
                                                        </option>
                                                    </select>
                                                </td>
                                                <td class="overflow-visible align-top">
                                                    <!-- Date/Time Radio Buttons -->
                                                    <div 
                                                        v-if="row.showDateTimeRadio"
                                                        class="divRadio marginbottom10 w-100 float-left"
                                                    >
                                                        <input 
                                                            type="radio"
                                                            :id="'IsTime_0_' + index"
                                                            :name="'IsTime_' + index"
                                                            value="0"
                                                            v-model="row.isTime"
                                                            class="rdIsTime"
                                                            @change="onDateTimeChange(index)"
                                                        />
                                                        <label :for="'IsTime_0_' + index" class="mr-3">{{ $t('Date') }}</label>
                                                        <input 
                                                            type="radio"
                                                            :id="'IsTime_1_' + index"
                                                            :name="'IsTime_' + index"
                                                            value="1"
                                                            v-model="row.isTime"
                                                            class="rdIsTime"
                                                            @change="onDateTimeChange(index)"
                                                        />
                                                        <label :for="'IsTime_1_' + index">{{ $t('Time') }}</label>
                                        </div>
                                                    
                                                    <!-- Value Input(s) -->
                                                    <div v-if="row.showValueInput">
                                                        <input 
                                                            v-if="row.isBetween"
                                                            type="text"
                                                            :id="'FieldFirstValue_' + index"
                                                            :name="'FieldFirstValue_' + index"
                                                            v-model="row.fieldFirstValue"
                                                            :class="getValueInputClass(row, index, 'first')"
                                                            :maxlength="row.maxLength"
                                                            :placeholder="row.placeholder"
                                                        />
                                                        <input 
                                                            v-if="row.isBetween"
                                                            type="text"
                                                            :id="'FieldSecondValue_' + index"
                                                            :name="'FieldSecondValue_' + index"
                                                            v-model="row.fieldSecondValue"
                                                            :class="getValueInputClass(row, index, 'second')"
                                                            :maxlength="row.maxLength"
                                                            :placeholder="row.placeholder"
                                                        />
                                            <input
                                                            v-if="!row.isBetween"
                                                type="text"
                                                            :id="'FieldFirstValue_' + index"
                                                            :name="'FieldFirstValue_' + index"
                                                            v-model="row.fieldFirstValue"
                                                            :class="getValueInputClass(row, index, 'first')"
                                                            :maxlength="row.maxLength"
                                                            :placeholder="row.placeholder"
                                            />
                                        </div>
                                                    
                                                    <!-- Off Days Checkboxes -->
                                                    <div 
                                                        v-if="row.showOffDays"
                                                        class="width_100 float-left chkOffDays margintop5"
                                                    >
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day1_' + index"
                                                                value="1"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day1_' + index" class="ml-1">{{ $t('Sun') }}</label>
                                                        </div>
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day2_' + index"
                                                                value="2"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day2_' + index" class="ml-1">{{ $t('Mon') }}</label>
                                                        </div>
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day3_' + index"
                                                                value="3"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day3_' + index" class="ml-1">{{ $t('Tue') }}</label>
                                                        </div>
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day4_' + index"
                                                                value="4"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day4_' + index" class="ml-1">{{ $t('Wed') }}</label>
                                                        </div>
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day5_' + index"
                                                                value="5"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day5_' + index" class="ml-1">{{ $t('Thu') }}</label>
                                                        </div>
                                                        <div class="mr-3 offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day6_' + index"
                                                                value="6"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day6_' + index" class="ml-1">{{ $t('Fri') }}</label>
                                                        </div>
                                                        <div class="offdays float-left">
                                                            <input 
                                                                type="checkbox" 
                                                                :id="'day7_' + index"
                                                                value="7"
                                                                v-model="row.daysOfWeek"
                                                                class="custCheckBox"
                                                            />
                                                            <label :for="'day7_' + index" class="ml-1">{{ $t('Sat') }}</label>
                                        </div>
                                    </div>
                                                </td>
                                                <td class="overflow-visible align-top">
                                                    <input 
                                                        type="text"
                                                        :id="'ClosingBrackets_' + index"
                                                        :name="'ClosingBrackets_' + index"
                                                        v-model="row.closingBrackets"
                                                        class="form-control txtClosingBrackets"
                                                        maxlength="5"
                                                    />
                                                </td>
                                                <td class="single-action overflow-visible align-top">
                                                    <a 
                                                        v-if="index > 0"
                                                        :title="$t('Delete')" 
                                                        class="addDiscount delRow" 
                                                        href="javascript:;"
                                                        @click="removeRow(index)"
                                                    >
                                                        <i class="fa fa-trash text-danger" aria-hidden="true"></i>
                                                    </a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                
                                <div class="col-lg-12 add-form-field text-right py-3 px-0">
                                    <a 
                                        href="javascript:;" 
                                        id="ancAddRow" 
                                        :title="$t('AddNewRow')" 
                                        class="btn btn-success"
                                        @click="addRow"
                                    >
                                        <i class="fa fa-plus pr-2" aria-hidden="true"></i>{{ $t('AddNewRow') }}
                                    </a>
                                </div>
                            </div>
                            
                            <!-- Condition Display -->
                            <div class="col-lg-12 mt-3">
                                <div class="heading-border mt-3 mb-2">
                                    <h5 class="h5"><span>{{ $t('Condition') }}:</span></h5>
                                </div>
                                <div class="mb-2" id="divCondition">{{ conditionText }}</div>
                            </div>
                        </div>

                        <div class="modal-footer">
                            <button class="btn btn-success" @click="onApply">
                                <em class="fa fa-check pr-2" aria-hidden="true"></em>{{ $t('ApplyFilter') }}
                            </button>
                            <button class="btn btn-danger" @click="onClose">
                                <em class="fa fa-close pr-2" aria-hidden="true"></em>{{ $t('Cancel') }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { locateBillingService } from '../../../services/locateBillingService';
import http from '../../../http-common';

export default {
    name: 'AdvanceFilter',
    props: {
        setupId: {
            type: [Number, String],
            default: null
        },
        templateId: {
            type: [Number, String],
            default: null
        },
        filterFields: {
            type: Array,
            default: () => []
        }
    },
    data() {
        return {
            isLoading: false,
            filterRows: [
                {
                    relationWithParent: '',
                    openingBrackets: '(',
                    templateId: '',
                    operatorId: '',
                    operatorList: [],
                    additionalTemplateId: '',
                    additionalOperatorId: '',
                    fieldFirstValue: '',
                    fieldSecondValue: '',
                    closingBrackets: ')',
                    isTime: '0',
                    daysOfWeek: [],
                    dataType: '',
                    operatorExpression: '',
                    showDateTimeRadio: false,
                    showValueInput: true,
                    showOffDays: false,
                    isBetween: false,
                    maxLength: 250,
                    placeholder: ''
                }
            ],
            relationList: [
                { Value: 'AND', Text: 'AND' },
                { Value: 'OR', Text: 'OR' }
            ],
            dateTimeFields: [],
            nvarcharFields: [],
            dateOperators: []
        };
    },
    computed: {
        conditionText() {
            var condition = '';
            var databaseCondition = '';
            
            this.filterRows.forEach((row, index) => {
                if (!row.templateId || !row.operatorId) {
                    return;
                }
                
                var field = this.getFieldById(row.templateId);
                if (!field) return;
                
                var operator = row.operatorList.find(op => op.OPERATOR_ID == row.operatorId);
                if (!operator) return;
                
                var operatorExpr = (operator.OPERATOR_EXPRESSION || '').toLowerCase();
                var dataType = (field.DATA_TYPE || '').toLowerCase();
                
                // Relation
                if (index > 0 && row.relationWithParent) {
                    condition += ' ' + row.relationWithParent + ' ';
                    databaseCondition += ' ' + row.relationWithParent + ' ';
                }
                
                // Opening bracket
                if (row.openingBrackets) {
                    condition += row.openingBrackets + ' ';
                    databaseCondition += row.openingBrackets + ' ';
                }
                
                // Field name
                condition += field.DISPLAY_NAME + ' ';
                var fieldSql = this.getFieldSqlName(field);
                databaseCondition += fieldSql + ' ';
                
                // Operator
                condition += operator.OPERATOR_DISPLAY_NAME + ' ';
                
                // Value handling
                if (operatorExpr === 'between') {
                    condition += row.fieldFirstValue + ' And ' + row.fieldSecondValue;
                    databaseCondition += 'BETWEEN ' + this.formatValue(row.fieldFirstValue, dataType) + ' AND ' + this.formatValue(row.fieldSecondValue, dataType);
                } else if (['is week end', 'is working day'].includes(operatorExpr)) {
                    var days = row.daysOfWeek.join(',');
                    databaseCondition += 'DATEPART(DW, ' + fieldSql + ') IN (' + days + ')';
                } else if (['is null', 'is not null', 'is duplicate', 'is lowest', 'is highest', 'is not lowest', 'is not highest'].includes(operatorExpr)) {
                    // No value needed
                } else {
                    condition += row.fieldFirstValue;
                    databaseCondition += this.getOperatorSql(operatorExpr) + ' ' + this.formatValue(row.fieldFirstValue, dataType);
                }
                
                // Closing bracket
                if (row.closingBrackets) {
                    condition += ' ' + row.closingBrackets;
                    databaseCondition += ' ' + row.closingBrackets;
                }
            });
            
            return condition || this.$t('NoConditionDefined');
        }
    },
    watch: {
        filterFields: {
            handler: function(newVal) {
                if (newVal && newVal.length > 0) {
                    this.prepareFieldLists();
                }
            },
            immediate: true
        },
        templateId: {
            handler: function(newVal) {
                if (newVal) {
                    this.loadOperatorsForAllRows();
                }
            }
        }
    },
    methods: {
        prepareFieldLists() {
            this.dateTimeFields = this.filterFields.filter(f => f.DATA_TYPE === 'datetime');
            this.nvarcharFields = this.filterFields.filter(f => f.DATA_TYPE === 'nvarchar');
        },
        getFieldById(templateId) {
            return this.filterFields.find(f => String(f.TEMPLATE_ID) === String(templateId));
        },
        getFieldSqlName(field) {
            var tableName = (field.TABLE_NAME || '').toLowerCase();
            var columnName = field.COLUMN_NAME || '';
            
            if (tableName === 'talygen_be_template_master') {
                return 'M.' + columnName;
            } else if (tableName === 'talygen_be_processed_records') {
                return 'R.' + columnName;
            } else if (tableName === 'talygen_client_additional_info') {
                return 'INFO.' + columnName;
            } else if (tableName === 'talygen_be_billing_division') {
                return 'DIV.' + columnName;
            } else if (tableName === 'talygen_cost_center') {
                return 'C_CENTER.' + columnName;
            } else if (tableName === 'talygen_be_sba_id') {
                return 'SBA.' + columnName;
            }
            return columnName;
        },
        getOperatorSql(operatorExpr) {
            var opMap = {
                'equal to': '=',
                'not equal to': '!=',
                'greater than': '>',
                'less than': '<',
                'greater than or equal to': '>=',
                'less than or equal to': '<=',
                'like': 'LIKE',
                'starts with': 'LIKE',
                'ends with': 'LIKE'
            };
            return opMap[operatorExpr] || operatorExpr.toUpperCase();
        },
        formatValue(value, dataType) {
            if (!value) return '';
            
            if (dataType === 'nvarchar' || dataType === 'datetime' || dataType === 'time') {
                return "'" + value + "'";
            }
            return value;
        },
        onFieldChange(index) {
            var row = this.filterRows[index];
            var field = this.getFieldById(row.templateId);
            
            if (!field) {
                this.resetRow(row);
                return;
            }
            
            row.dataType = field.DATA_TYPE;
            this.loadOperatorsForRow(index);
            this.updateRowDisplay(row);
        },
        onOperatorChange(index) {
            var row = this.filterRows[index];
            var operator = row.operatorList.find(op => op.OPERATOR_ID == row.operatorId);
            
            if (!operator) {
                this.updateRowDisplay(row);
                return;
            }
            
            row.operatorExpression = operator.OPERATOR_EXPRESSION;
            this.updateRowDisplay(row);
        },
        onAdditionalFieldChange(index) {
            // Handle additional field change for date diff operators
        },
        onDateTimeChange(index) {
            var row = this.filterRows[index];
            // Update date/time picker based on selection
        },
        updateRowDisplay(row) {
            var operatorExpr = (row.operatorExpression || '').toLowerCase();
            var dataType = (row.dataType || '').toLowerCase();
            
            // Reset all display flags
            row.showDateTimeRadio = false;
            row.showValueInput = true;
            row.showOffDays = false;
            row.isBetween = false;
            row.maxLength = 250;
            row.placeholder = '';
            
            // Date/Time operators
            if (dataType === 'datetime') {
                row.showDateTimeRadio = true;
                if (operatorExpr === 'between') {
                    row.isBetween = true;
                }
            }
            
            // Special operators that don't need values
            if (['is week end', 'is working day', 'is off day', 'is duplicate', 'is null', 'is not null', 
                 'is lowest', 'is highest', 'is not lowest', 'is not highest'].includes(operatorExpr)) {
                row.showValueInput = false;
                if (['is week end', 'is working day'].includes(operatorExpr)) {
                    row.showOffDays = true;
                }
            }
            
            // Between operator
            if (operatorExpr === 'between') {
                row.isBetween = true;
            }
            
            // Set max length based on data type
            if (dataType === 'int') {
                row.maxLength = 9;
            } else if (dataType === 'bigint') {
                row.maxLength = 18;
            } else if (dataType === 'float') {
                row.maxLength = 15;
            } else if (dataType === 'decimal') {
                row.maxLength = 21;
            }
        },
        showAdditionalFields(row) {
            var operatorExpr = (row.operatorExpression || '').toLowerCase();
            return ['minutesdiff', 'hoursdiff', 'daydiff', 'monthdiff', 'yeardiff', 'has with'].includes(operatorExpr);
        },
        getAdditionalFieldList(row) {
            var operatorExpr = (row.operatorExpression || '').toLowerCase();
            if (['minutesdiff', 'hoursdiff', 'daydiff', 'monthdiff', 'yeardiff'].includes(operatorExpr)) {
                return this.dateTimeFields;
            } else if (operatorExpr === 'has with') {
                return this.nvarcharFields;
            }
            return [];
        },
        getAdditionalOperatorList(row) {
            // Return appropriate operators for additional field
            return row.operatorList || [];
        },
        getValueInputClass(row, index, type) {
            var classes = ['form-control'];
            
            if (row.isBetween) {
                if (type === 'first') {
                    classes.push('txtFirstValue', 'w-48', 'float-left');
                } else {
                    classes.push('txtSecondValue', 'w-48', 'float-right');
                }
            } else {
                classes.push('txtFirstValue');
            }
            
            // Add date/time picker classes if needed
            if (row.dataType === 'datetime' && row.isTime === '0') {
                classes.push('txtdatepicker');
            } else if (row.dataType === 'datetime' && row.isTime === '1') {
                classes.push('txttimepicker');
            } else if (row.dataType === 'time') {
                classes.push('txttimepicker');
            }
            
            return classes.join(' ');
        },
        resetRow(row) {
            row.operatorId = '';
            row.operatorList = [];
            row.additionalTemplateId = '';
            row.additionalOperatorId = '';
            row.fieldFirstValue = '';
            row.fieldSecondValue = '';
            row.dataType = '';
            row.operatorExpression = '';
            this.updateRowDisplay(row);
        },
        loadOperatorsForRow(index) {
            var vm = this;
            var row = vm.filterRows[index];
            
            if (!row.templateId) {
                row.operatorList = [];
                return;
            }
            
            var field = this.getFieldById(row.templateId);
            if (!field) {
                row.operatorList = [];
                return;
            }
            
            vm.isLoading = true;
            
            // Load operators based on field's data type
            // This should call LoadAdvanceFilterOperators endpoint
            http.instance.get(`/BillingService/LoadAdvanceFilterOperators?templateId=${row.templateId}`).then((response) => {
                if (response && response.data) {
                    row.operatorList = Array.isArray(response.data) ? response.data : [];
                } else {
                    row.operatorList = [];
                }
                vm.isLoading = false;
            }).catch(() => {
                // Fallback: use default operators based on data type
                row.operatorList = this.getDefaultOperators(field.DATA_TYPE);
                vm.isLoading = false;
            });
        },
        getDefaultOperators(dataType) {
            var operators = [];
            var type = (dataType || '').toLowerCase();
            
            if (type === 'int' || type === 'bigint' || type === 'float' || type === 'decimal') {
                operators = [
                    { OPERATOR_ID: 1, OPERATOR_DISPLAY_NAME: 'Equal To', OPERATOR_EXPRESSION: 'equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 2, OPERATOR_DISPLAY_NAME: 'Not Equal To', OPERATOR_EXPRESSION: 'not equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 3, OPERATOR_DISPLAY_NAME: 'Greater Than', OPERATOR_EXPRESSION: 'greater than', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 4, OPERATOR_DISPLAY_NAME: 'Less Than', OPERATOR_EXPRESSION: 'less than', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 5, OPERATOR_DISPLAY_NAME: 'Greater Than Or Equal To', OPERATOR_EXPRESSION: 'greater than or equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 6, OPERATOR_DISPLAY_NAME: 'Less Than Or Equal To', OPERATOR_EXPRESSION: 'less than or equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 7, OPERATOR_DISPLAY_NAME: 'Between', OPERATOR_EXPRESSION: 'between', OPERATION_DATA_TYPE: type }
                ];
            } else if (type === 'datetime' || type === 'time') {
                operators = [
                    { OPERATOR_ID: 1, OPERATOR_DISPLAY_NAME: 'Equal To', OPERATOR_EXPRESSION: 'equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 2, OPERATOR_DISPLAY_NAME: 'Not Equal To', OPERATOR_EXPRESSION: 'not equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 3, OPERATOR_DISPLAY_NAME: 'Greater Than', OPERATOR_EXPRESSION: 'greater than', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 4, OPERATOR_DISPLAY_NAME: 'Less Than', OPERATOR_EXPRESSION: 'less than', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 7, OPERATOR_DISPLAY_NAME: 'Between', OPERATOR_EXPRESSION: 'between', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 8, OPERATOR_DISPLAY_NAME: 'Is Week End', OPERATOR_EXPRESSION: 'is week end', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 9, OPERATOR_DISPLAY_NAME: 'Is Working Day', OPERATOR_EXPRESSION: 'is working day', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 10, OPERATOR_DISPLAY_NAME: 'Is Null', OPERATOR_EXPRESSION: 'is null', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 11, OPERATOR_DISPLAY_NAME: 'Is Not Null', OPERATOR_EXPRESSION: 'is not null', OPERATION_DATA_TYPE: type }
                ];
            } else if (type === 'nvarchar' || type === 'varchar' || type === 'string') {
                operators = [
                    { OPERATOR_ID: 1, OPERATOR_DISPLAY_NAME: 'Equal To', OPERATOR_EXPRESSION: 'equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 2, OPERATOR_DISPLAY_NAME: 'Not Equal To', OPERATOR_EXPRESSION: 'not equal to', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 12, OPERATOR_DISPLAY_NAME: 'Like', OPERATOR_EXPRESSION: 'like', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 13, OPERATOR_DISPLAY_NAME: 'Starts With', OPERATOR_EXPRESSION: 'starts with', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 14, OPERATOR_DISPLAY_NAME: 'Ends With', OPERATOR_EXPRESSION: 'ends with', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 15, OPERATOR_DISPLAY_NAME: 'In', OPERATOR_EXPRESSION: 'in', OPERATION_DATA_TYPE: type },
                    { OPERATOR_ID: 16, OPERATOR_DISPLAY_NAME: 'Not In', OPERATOR_EXPRESSION: 'not in', OPERATION_DATA_TYPE: type }
                ];
            }
            
            return operators;
        },
        loadOperatorsForAllRows() {
            this.filterRows.forEach((row, index) => {
                if (row.templateId) {
                    this.loadOperatorsForRow(index);
                }
            });
        },
        addRow() {
            this.filterRows.push({
                relationWithParent: '',
                openingBrackets: '(',
                templateId: '',
                operatorId: '',
                operatorList: [],
                additionalTemplateId: '',
                additionalOperatorId: '',
                fieldFirstValue: '',
                fieldSecondValue: '',
                closingBrackets: ')',
                isTime: '0',
                daysOfWeek: [],
                dataType: '',
                operatorExpression: '',
                showDateTimeRadio: false,
                showValueInput: true,
                showOffDays: false,
                isBetween: false,
                maxLength: 250,
                placeholder: ''
            });
        },
        removeRow(index) {
            if (this.filterRows.length > 1) {
                this.filterRows.splice(index, 1);
            }
        },
        onApply() {
            // Build filter data and emit
            var filterData = {
                conditions: this.filterRows.map(row => ({
                    relationWithParent: row.relationWithParent,
                    openingBrackets: row.openingBrackets,
                    templateId: row.templateId,
                    operatorId: row.operatorId,
                    additionalTemplateId: row.additionalTemplateId,
                    additionalOperatorId: row.additionalOperatorId,
                    fieldFirstValue: row.fieldFirstValue,
                    fieldSecondValue: row.fieldSecondValue,
                    closingBrackets: row.closingBrackets,
                    isTime: row.isTime,
                    daysOfWeek: row.daysOfWeek.join(',')
                })),
                conditionText: this.conditionText
            };
            
            this.$emit('apply', filterData);
        },
        onClose() {
            this.$emit('close');
        }
    }
};
</script>

