<template>
    <section>
        <div class="my-popups">
            <Loader :is-visible="isLoading" />
            <div class="modal d-block" style="z-index: 1050;">
                <div class="modal-dialog modal-dialog-centered modal-lg">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">{{ $t('Manageworkcode') }}</h5>
                            <button type="button" class="close text-white" @click="onClose">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>
                        <div class="modal-body">
                            <span class="user-guide">
                                <a 
                                    class="ancuserguide" 
                                    data-toggle="collapse" 
                                    href="#collapseChange_work_Code" 
                                    role="button" 
                                    aria-expanded="false" 
                                    aria-controls="collapseExample"
                                >
                                    {{ $t('UserGuide') }}
                                </a>
                                <div id="collapseChange_work_Code" class="divancuserguide collapse">
                                    <div id="scrollbarreplyuserguide">
                                        <div class="con">
                                            <div v-html="$t('User_Guide_Invoice_Change_work_Code')"></div>
                                        </div>
                                    </div>
                                </div>
                            </span>
                            <div class="row">
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label>{{ $t('WorkCode') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control ddlworkcode"
                                            v-model="form.WorkCodeId"
                                            :class="{ 'is-invalid': errors.WorkCodeId }"
                                        >
                                            <option value="">{{ $t('SelectWorkCode') }}</option>
                                            <option
                                                v-for="item in workCodeOptions"
                                                :key="item.Value"
                                                :value="item.Value"
                                            >
                                                {{ item.Name || item.Text }}
                                            </option>
                                        </select>
                                        <span v-if="errors.WorkCodeId" class="invalid-feedback">{{ errors.WorkCodeId }}</span>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="inp-group">
                                        <label>{{ $t('IsAdder') }}</label>
                                        <div class="form-check pl-0 pt-1">
                                            <input
                                                type="checkbox"
                                                id="IsAdderSelectedN"
                                                class="form-check-input"
                                                v-model="form.IsAdder"
                                                @change="onIsAdderChange"
                                            />
                                            <input type="hidden" id="IsAdderSelected" :value="form.IsAdder" />
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="row" v-if="form.IsAdder" id="divChangeAdderNameLineIteams">
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{ $t('AdderName') }}<span class="mandatory">*</span></label>
                                        <select
                                            class="form-control ddladdernamelist"
                                            v-model="form.AdderName"
                                            :class="{ 'is-invalid': errors.AdderName }"
                                        >
                                            <option value="">{{ $t('SelectAdder') }}</option>
                                            <option
                                                v-for="item in adderOptions"
                                                :key="item.Value"
                                                :value="item.Name || item.Value"
                                            >
                                                {{ item.Name || item.Value }}
                                            </option>
                                        </select>
                                        <span v-if="errors.AdderName" class="invalid-feedback" id="spnAdderName">{{ errors.AdderName }}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <div class="row flex-row-reverse w-100 mt-2">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" class="btn btn-success" @click="onSave" :title="$t('Save')">
                                        <i class="fa fa-save pr-2"></i>{{ $t('Save') }}
                                    </a>
                                    <a class="btn btn-danger" href="javascript:;" @click="onClose" :title="$t('Cancel')">
                                        <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                    </a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2">
                                        <span class="mandatory">*</span> {{ $t('MandatoryField') }}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { locateBillingService } from '../../../services/locateBillingService';
import Loader from '../../../components/Common/Loader.vue';

export default {
    components: {
        Loader
    },
    name: 'ManageWorkCodeInvoice',
    props: {
        lineItemIds: {
            type: [String, Array],
            default: ''
        },
        requestFrom: {
            type: String,
            default: 'lineitem'
        },
        allInvoice: {
            type: String,
            default: ''
        },
        xmlData: {
            type: String,
            default: ''
        },
        lineBatchId: {
            type: String,
            default: ''
        },
        invoiceBillId: {
            type: [Number, String],
            default: null
        }
    },
    data() {
        return {
            isLoading: false,
            workCodeOptions: [],
            adderOptions: [],
            form: {
                WorkCodeId: '',
                IsAdder: false,
                AdderName: ''
            },
            errors: {
                WorkCodeId: '',
                AdderName: ''
            }
        };
    },
    created() {
        this.loadWorkCodes();
    },
    methods: {
        loadWorkCodes() {
            this.isLoading = true;
            locateBillingService.GetWorkCodeData()
                .then(response => {
                    this.isLoading = false;
                    if (response.data) {
                        this.workCodeOptions = response.data;
                        this.adderOptions = response.data;
                    }
                })
                .catch(() => {
                    this.isLoading = false;
                });
        },
        onIsAdderChange() {
            var vm = this;
            if (!vm.form.IsAdder) {
                vm.form.AdderName = '';
                vm.errors.AdderName = '';
            }
        },
        validate() {
            var vm = this;
            vm.errors = {
                WorkCodeId: '',
                AdderName: ''
            };
            var hasError = false;

            if (!vm.form.WorkCodeId || vm.form.WorkCodeId === '') {
                vm.errors.WorkCodeId = vm.$t('Required').replace('{0}', vm.$t('WorkCode'));
                hasError = true;
            }

            if (vm.form.IsAdder && (!vm.form.AdderName || vm.form.AdderName === '')) {
                vm.errors.AdderName = vm.$t('Required').replace('{0}', vm.$t('AdderName'));
                hasError = true;
            }

            return !hasError;
        },
        async onSave() {
            var vm = this;
            if (!vm.validate()) {
                vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning", vm.$t('Alert'));
                return;
            }

            vm.isLoading = true;
            try {
                var postData = {
                    workCodeId: vm.form.WorkCodeId,
                    workCode: vm.getWorkCodeText(vm.form.WorkCodeId),
                    lineIds: Array.isArray(vm.lineItemIds) ? vm.lineItemIds.join(',') : vm.lineItemIds,
                    reqFrom: vm.requestFrom,
                    allInvoice: vm.allInvoice,
                    xmlData: vm.xmlData,
                    isAdder: vm.form.IsAdder ? 'true' : 'false',
                    adderName: vm.form.AdderName || '',
                    lineBatchId: vm.lineBatchId,
                    invoiceBillId: vm.invoiceBillId
                };

                var response = await locateBillingService.SaveWorkCode(postData);
                if (response && response.data) {
                    if (response.data.flag === 'success') {
                        vm.ShowAlert(response.data.msg || vm.$t('SavedSuccessfully'), "success", vm.$t('Alert'));
                        vm.$emit('saved');
                        vm.onClose();
                    } else {
                        vm.ShowAlert(response.data.msg || vm.$t('ErrorWhileProcessingRequest'), "failure", vm.$t('Alert'));
                    }
                } else {
                    vm.ShowAlert(vm.$t('ErrorWhileProcessingRequest'), "failure", vm.$t('Alert'));
                }
            } catch (error) {
                console.log('Error saving work code:', error);
                vm.ShowAlert(vm.$t('ErrorWhileProcessingRequest'), "failure", vm.$t('Alert'));
            } finally {
                vm.isLoading = false;
            }
        },
        getWorkCodeText(id) {
            const opt = this.workCodeOptions.find(x => x.Value === id);
            return opt ? opt.Name : '';
        },
        onClose() {
            this.$emit('close');
        }
    }
};
</script>

<style scoped>
.mandatory {
    color: red;
}
.is-invalid {
    border-color: #dc3545;
}
.invalid-feedback {
    display: block;
    width: 100%;
    margin-top: 0.25rem;
    font-size: 0.875rem;
    color: #dc3545;
}
.modal {
    background-color: rgba(0, 0, 0, 0.5);
}
</style>


