                                        id="DivisionId" 
                                        name="DivisionId" 
                                        class="form-control"
                                        :class="{ 'is-invalid': validationErrors.DivisionId }"
                                        v-model="searchData.DivisionId"
                                        @change="onDivisionChange"
                                    >
                                        <option value="">{{ $t('Select') }}</option>
                                        <option 
                                            v-for="item in divisionList" 
                                            :key="item.ID" 
                                            :value="item.ID"
