                                        </option>
                                    </select>
                                    <div v-if="validationErrors.DivisionId" class="invalid-feedback d-block">{{ validationErrors.DivisionId }}</div>
                                </div>
                            </div>
                            <div class="col-lg-4">
                                <div class="form-group">
