                                    <div class="row">
                                        <div class="col-lg-6">
                                            <div class="input-group">
                                                        <v-date-picker id="dateFrom" mode="date" class="timedate input-group" v-model="searchData.dateFrom">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input placeholder="MM/DD/YYYY" @click="togglePopover()" class="form-control" :class="{ 'is-invalid': validationErrors.dateFrom }" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append">
                                                                    <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                </div>
                                                            </template>
                                                        </v-date-picker>
                                            </div>
                                            <div v-if="validationErrors.dateFrom" class="invalid-feedback d-block">{{ validationErrors.dateFrom }}</div>
                                            </div>
                                        <div class="col-lg-6">
                                            <div class="input-group">
                                                        <v-date-picker id="dateTo" mode="date" class="timedate input-group" v-model="searchData.dateTo">
                                                            <template v-slot="{ inputValue, inputEvents, togglePopover }">
                                                        <input placeholder="MM/DD/YYYY" @click="togglePopover()" class="form-control" :class="{ 'is-invalid': validationErrors.dateTo }" :value="inputValue" v-on="inputEvents" />
                                                                <div @click="togglePopover()" class="input-group-append">
                                                                    <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                                </div>
                                                            </template>
                                                        </v-date-picker>
                                                </div>
                                            <div v-if="validationErrors.dateTo" class="invalid-feedback d-block">{{ validationErrors.dateTo }}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a href="javascript:;" class="btn btn-success mr-2" id="Go" @click="onSearch" :title="$t('Search')">
                                    <em class="fa fa-search pr-2"></em>{{ $t('Search') }}
                                </a>
                                <a href="javascript:;" class="btn btn-danger" id="aClear" @click="onClear" :title="$t('Clear')">
                                    <em class="fa fa-close pr-2"></em>{{ $t('Clear') }}
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <span class="mandatory">{{ $t('MandatoryString') }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
        
        <!-- Main Form (shown when BillId > 0 or after search) -->
        <div class="w-100" id="divMain" :style="{ display: billId > 0 || showMainForm ? 'block' : 'none' }">
