                <div class="container-fluid">
                    <div class="row">
                        <div class="col-md-12 padding-t_8">
                            <div class="theme-primary partition-full">
                                <span class="p-name text-white">{{ pageTitle }}</span>
                                <span class="p-actions float-md-right float-left mb-wid-100">
                                    <a 
                                        v-if="billId > 0"
                                        :href="backUrl" 
                                        class="p-action-btn text-white d-none d-md-inline-block"
                                    >
                                        <em class="fa fa-angle-double-left" aria-hidden="true"></em><br>{{ $t('BacktoList') }}
                                    </a>
                                    <a 
                                        v-else
                                        href="javascript:;" 
                                        id="aBacktoSearch" 
                                        @click="onBackToSearch"
                                        class="p-action-btn text-white d-none d-md-inline-block"
                                    >
                                        <em class="fa fa-angle-double-left" aria-hidden="true"></em><br />{{ $t('BackToSearch') }}
                                    </a>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            
