                                        class="form-control disabled-grey" 
                                        readonly="readonly" 
                                        id="txtClientName" 
                                    />
                                    <input type="hidden" name="BillTo" id="hdnBillTo" :value="invoiceData.ClientId" />
                                </div>
                            </div>
                            <div class="col-lg-4">
                                <div class="form-group">
                                    <label>{{ $t('BillNumber') }}:<span class="mandatory">*</span></label>
                                    <input 
                                        v-if="invoiceData.BillNumber === null || invoiceData.BillNumber === 'Draft Mode'"
                                        type="text" 
                                        class="form-control disabled-grey" 
                                        readonly="readonly" 
                                        :value="$t('InvoiceNumberGenratesAutomatically').replace('{0}', $t('Invoice'))" 
                                    />
