                                        id="hdnBillNumber" 
                                        name="hdnBillNumber" 
                                        type="hidden" 
                                        :value="invoiceData.LatestInvoiceNo || invoiceData.InvoiceStartNo" 
                                    />
                                </div>
                            </div>
