                                        id="ShowNumber" 
                                        class="ChangeStatus" 
                                        typefor="ShowNumber" 
                                        href="javascript:;"
                                        @click="onChangeInvoiceStatus('ShowNumber')"
                                    >
                                        {{ $t('SwitchToGenerateInvoiceNumber') }}
                                    </a>
                                </div>
                            </div>
