                                    <label>{{ $t('DateRange') }}:</label>
                                    <div class="row">
                                        <div class="col-lg-6">
                                            <div class="input-group">
                                                <v-date-picker id="DateRangeFrom" mode="date" class="timedate input-group" v-model="invoiceData.DateRangeFrom">
