                                                        <div @click="togglePopover()" class="input-group-append">
                                                            <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                        </div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="input-group">
                                                <v-date-picker id="DateRangeTo" mode="date" class="timedate input-group" v-model="invoiceData.DateRangeTo">
                                                    <template v-slot="{ inputValue, inputEvents, togglePopover }">
