                                                        <div @click="togglePopover()" class="input-group-append">
                                                            <span class="input-group-text"><em class="fa fa-calendar"></em></span>
                                                        </div>
                                                    </template>
                                                </v-date-picker>
                                            </div>
                                        </div>
                                    </div>
                                    <input type="hidden" v-model="invoiceData.BillDateFrom" id="hdnDateFrom" />
                                    <input type="hidden" v-model="invoiceData.BillDateTo" id="hdnDateTo" />
                                </div>
                            </div>
                            <div class="col-lg-4">
                                <div class="form-group">
                                    <label>{{ $t('DueDate') }}:<span class="mandatory">*</span></label>
                                    <div class="input-group">
                                        <v-date-picker id="BillDueDate" mode="date" class="timedate input-group" v-model="invoiceData.BillDueDate">
