                                        type="text" 
                                        v-model="invoiceData.Terms" 
                                        class="form-control text txtblank" 
                                        maxlength="200" 
                                    />
                                </div>
                            </div>
                            <div class="col-lg-4">
                                <div class="form-group">
                                    <label>{{ $t('Comment') }}:</label>
                                    <input 
                                        type="text" 
                                        v-model="invoiceData.BillDateComments" 
                                        class="form-control text txtblank" 
                                        maxlength="200" 
                                    />
                                </div>
                            </div>
                            <div class="col-lg-4">
                                <div class="form-group">
                                    <label>{{ $t('InvoiceMemo') }}:</label>
                                    <input 
                                        type="text" 
                                        v-model="invoiceData.InvoiceMemo" 
                                        class="form-control" 
                                        maxlength="200" 
                                    />
                                </div>
                            </div>
