                                <div class="form-group">
                                    <label>{{ $t('InvoicePONumber') }}:</label>
                                    <input 
                                        type="text" 
                                        v-model="invoiceData.InvoicePONumber" 
                                        class="form-control" 
                                        maxlength="200" 
                                    />
                                </div>
                            </div>
