                                <div class="form-group">
                                    <label>{{ $t('PaymentTerm') }}:<span class="mandatory">*</span></label>
                                    <select 
                                        v-model="invoiceData.PaymentTermId" 
                                        class="form-control"
                                    >
                                        <option value="">{{ $t('Select') }}</option>
                                        <option 
                                            v-for="term in paymentTermsList" 
                                            :key="term.Value" 
                                            :value="term.Value"
                                        >
                                            {{ term.Text }}
                                        </option>
                                    </select>
                                </div>
                            </div>
