                                                v-if="getNotesByCompanyId === 'yes' && invoiceData.DivisionId"
                                                    href="javascript:;" 
                                                    :data-uniquename="'note_' + invoiceData.DivisionId" 
                                                    :data-divisionid="invoiceData.DivisionId" 
                                                    :id="'note_' + invoiceData.DivisionId" 
                                                    class="text-dark enable viewnotesdivision"
                                                    @click="onViewNotes"
                                                    data-toggle-tooltip="tooltip"
                                                    :data-original-title="invoiceData.LatestNote || 'View'"
                                                    data-placement="top"
                                            >
                                                <em class="fa fa-file-text-o" aria-hidden="true"></em>
                                                <span class="noti-circle noti-orange">{{ invoiceData.NoOfNotes || 0 }}</span>
                                            </a>
                                        </span>
                                    </div>
                                </div>
                            </div>
