                                <div class="form-group">
                                    <label>{{ $t('Project') }}</label>
                                    <select 
                                        v-model="invoiceData.ProjectId" 
                                        class="form-control ddlInvoiceProjectID"
                                    >
                                        <option value="">{{ $t('SelectProject') }}</option>
                                        <option 
                                            v-for="project in projectList" 
                                            :key="project.Value" 
                                            :value="project.Value"
                                        >
                                            {{ project.Text }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-lg-4" v-if="isShowInvoiceDetailDropdown">
                                <div class="form-group">
                                    <label>{{ $t('InvoiceDetailTemplate') }}:<span class="mandatory">*</span></label>
                                    <select 
                                        v-model="invoiceData.TemplateId" 
                                        id="ddlInvoiceDetailTemplate"
                                        class="form-control"
                                        @change="onTemplateChange"
                                    >
                                        <option value="">{{ $t('Select') }}</option>
                                        <option 
                                            v-for="template in invoiceDetailTemplateList" 
                                            :key="template.Value" 
                                            :value="template.Value"
                                        >
                                            {{ template.Name }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-lg-4" v-if="isShowWorkdayInvoiceType">
                                <div class="form-group">
                                    <label>{{ $t('WorkdayInvoiceType') }}:<span class="mandatory">*</span></label>
                                    <select 
                                        v-model="invoiceData.InvoiceTypeId" 
                                        id="InvoiceTypeId"
                                        class="form-control"
                                    >
                                        <option value="">{{ $t('Select') }}</option>
                                        <option 
                                            v-for="type in invoiceTypeList" 
                                            :key="type.Value" 
                                            :value="type.Value"
                                        >
                                            {{ type.Text }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-lg-4" v-if="isShowInvoiceDetailDropdown">
                                <div class="form-group">
                                    <label>{{ $t('DetailFileFormat') }}:<span class="mandatory">*</span></label>
                                    <select 
                                        v-model="invoiceData.DetailFileFormat" 
                                        class="form-control"
                                    >
                                        <option value="">{{ $t('Select') }}</option>
                                        <option 
                                            v-for="format in detailFileFormatList" 
                                            :key="format.Value" 
                                            :value="format.Value"
                                        >
                                            {{ format.Name }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                        </div>
                        
