                                <div class="heading-border mt-3 mb-2">
                                    <h5 class="h5">
                                        <span>{{ $t('InvoiceItems') }}</span>
                                        <small class="float-right">
                                            <a class="invaction" href="javascript:;" @click="toggleActionMenu">
                                                <em class="fa fa-plus pr-2"></em>{{ $t('Action') }}
                                            </a>
                                        </small>
                                    </h5>
                                </div>
                                
                                <!-- Action Menu -->
                                <span 
                                    class="action-list-box expand" 
                                    :style="{ 
                                        width: '224px', 
                                        display: showActionMenu ? 'block' : 'none',
                                        borderBottom: 'none !important',
                                        top: '-200px',
                                        right: '15px'
                                    }"
                                >
                                    <span class="list-actions w-100 fsm-list" id="action-list" style="font-size: 14px;">
                                        <a 
                                            :title="$t('NonVisibleInSummary')" 
                                            href="javascript:;" 
                                            class="actions-onclick" 
                                            id="ancNonVisibleInSummary"
                                            @click="onNonVisibleInSummary"
                                        >
                                            <em class="fa fa-eye-slash text-success action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('NonVisibleInSummary') }}</span>
                                        </a>
                                        <a 
                                            :title="$t('VisibleInSummary')" 
                                            href="javascript:;" 
                                            class="actions-onclick" 
                                            id="ancVisibleInSummary"
                                            @click="onVisibleInSummary"
                                        >
                                            <em class="fa fa-eye text-success action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('VisibleInSummary') }}</span>
                                        </a>
                                        <a 
                                            :title="$t('Manageworkcode')" 
                                            href="javascript:;" 
                                            class="actions-onclick" 
                                            id="changeworkCodeLineItems"
                                            @click="onManageWorkCode"
                                        >
                                            <em class="fa fa-barcode text-primary action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('Manageworkcode') }}</span>
                                        </a>
                                        <a 
                                            id="changeColumnValueLineItems" 
                                            class="actions-onclick" 
                                            href="javascript:;" 
                                            :title="$t('Update')"
                                            @click="onChangeColumnValue"
                                        >
                                            <em class="fa fa-pencil text-success action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('Update') }}</span>
                                        </a>
                                        <a 
                                            v-if="billId > 0"
                                            id="addworkCodeLineItems" 
                                            class="actions-onclick" 
                                            href="javascript:;" 
                                            :title="$t('CloneLineItem')"
                                            @click="onCloneLineItem"
                                        >
                                            <em class="fa fa-plus text-success action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('CloneLineItem') }}</span>
                                        </a>
                                        <a 
                                            :title="$t('BulkMove')" 
                                            href="javascript:;" 
                                            class="actions-onclick" 
                                            id="changeContract"
                                            @click="onChangeContract"
                                        >
                                            <em class="fa fa-arrows text-info action-icon fa-size pr-2"></em>
                                            <span class="text">{{ $t('BulkMove') }}</span>
                                        </a>
                                        <em 
