                                                                v-for="field in filterFields" 
                                                                :key="field.TEMPLATE_ID" 
                                                                :value="field.TEMPLATE_ID"
                                                                :data-type="field.DATA_TYPE"
                                                                :data-columnname="field.COLUMN_NAME"
                                                                :data-tablename="field.TABLE_NAME"
                                                            >
                                                                {{ field.DISPLAY_NAME }}
                                                            </option>
                                                        </select>
                                                    </div>
                                                </div>
                                                <div class="col-12 col-sm-12 col-lg-2 col-xl-2">
                                                    <div class="input-group mb-2 mb-lg-0" id="divShowPopColumn">
                                                        <input 
                                                            type="text" 
                                                            :placeholder="$t('Value')" 
                                                            id="txtColumnValue" 
                                                            name="txtColumnValue" 
                                                            class="form-control" 
                                                            v-model="filterData.columnValue"
                                                            @keyup.enter="onApplyFilter"
                                                        />
                                                        <div id="divShowPopField" class="input-group-append d-none">
                                                            <span class="input-group-text">
                                                                <a href="javascript:;" data-toggle="popoveruserguide" :data-content="$t('InfoTextSearch')">
                                                                    <em class="fa fa-question-circle-o text-popover" style="font-size: 14px;"></em>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="col-12 col-sm-12 col-lg-6 col-xl-6">
                                                    <div class="search-btm-btn background-0 text-left">
                                                        <a 
                                                            id="aAddFilter" 
                                                            href="javascript:;" 
                                                            class="btn btn-primary" 
                                                            :title="$t('ApplyFilter')"
                                                            @click="onApplyFilter"
                                                        >
                                                            <em class="fa fa-search text-white mt-1 mr-2"></em>
                                                            <span class="text-white">{{ $t('ApplyFilter') }}</span>
                                                        </a>
                                                        <a 
                                                            :title="$t('Advance') + ' ' + $t('Filter')" 
                                                            id="Advancefilter" 
                                                            href="javascript:;" 
                                                            class="btn btn-dark text-white float-right"
                                                            @click="onAdvanceFilter"
                                                        >
                                                            <em class="fa fa-filter pr-2"></em>
                                                            <span class="text-white">{{ $t('Advance') }} {{ $t('Filter') }}</span>
                                                        </a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                                <!-- Active Filters -->
                                <div class="col-12 mb-2 mt-1 float-left" v-if="activeFilters.length > 0">
                                    <div class="py-2 w-100" id="divFiltersList">
                                        <button 
                                            v-for="(filter, index) in activeFilters" 
                                            :key="index"
                                            type="button" 
                                            class="btn p-2 mr-2 float-left search-tag aFiltera"
                                            :fieldType="filter.fieldType"
                                            :fieldId="filter.fieldId"
                                            :tablename="filter.tablename"
                                            :columnname="filter.columnname"
                                            :fieldValue="filter.fieldValue"
                                        >
                                            <span class="tag-name mw-100">{{ filter.displayName }} : {{ filter.fieldValue }}</span>
                                            <span class="badge aFilter">
                                                <a 
                                                    :title="$t('RemoveFilter')" 
                                                    href="javascript:;"
                                                    @click="removeFilter(index)"
                                                ></a>
                                            </span>
                                        </button>
                                    </div>
                                </div>
                                
                                <!-- Legend -->
                                <div class="col-12 mb-2 mt-1 float-left">
                                    <div class="mr-2 d-inline-block">
                                        <span class="legend-Progress colorboxsquare"></span>{{ $t('InsertedManually') }}
                                    </div>
                                    <div class="mr-2 d-inline-block">
                                        <span class="legend-pending colorboxsquare"></span>{{ $t('NonVisibleInSummary') }}
                                    </div>
                                </div>
                                
                                <!-- Line Items Table -->
                                <div class="listing">
                                    <div class="col-12" id="divLineItems">
                                        <LineItems 
                                            :lineItems="lineItemsData"
                                            :headers="lineItemHeaders"
                                            :subTotal="invoiceData.SubTotal"
                                            :creditAmountBalance="creditAmountBalance"
                                            :notInSummaryCost="notInSummaryCost"
                                            :selectAllInvoice="selectAllInvoice"
                                            @select-all-change="onSelectAllChange"
                                            @item-checkbox-change="onItemCheckboxChange"
                                            @column-sort="onColumnSort"
                                            @add-discount="onAddDiscount"
                                            @delete-discount="onDeleteDiscount"
                                            @edit-line-item="onEditLineItem"
                                        />
                                    </div>
                                    
                                    <!-- Pagination -->
                                    <div v-if="lineItemsPager && lineItemsPager.TotalRecords > 0" class="col-12 mt-3">
                                        <Pager
                                            ref="lineItemsPagerComponent"
                                            :numberofrecords="lineItemsPager.TotalRecords"
                                            :prevText="$t('Previous')"
                                            :nextText="$t('Next')"
                                            :firstText="$t('First')"
                                            :lastText="$t('Last')"
                                            :CurrentPageNumber="currentPage"
                                            :callbackfunction="onPageChange"
                                        />
                                    </div>
                                    
                                    <!-- Subtotal and Add New Row -->
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                        <div class="table-heading-wb leave-details bg-light mb-4 border">
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                                                <div class="row justify-content-end">
                                                    <div class="form-group form-inline mb-0">
                                                        <label class="col-form-label mr-2">{{ $t('SubTotal') }}:</label>
                                                        <input 
                                                            id="SubTotal"
                                                            type="text" 
                                                            v-model="invoiceData.SubTotal" 
                                                            style="pointer-events:none;" 
                                                            class="form-control" 
                                                            maxlength="200" 
                                                        />
                                                    </div>
                                                    <div class="mx-2">
                                                        <div class="search-btm-btn background-0 text-left py-3">
                                                            <a 
                                                                href="javascript:;" 
                                                                class="btn btn-success" 
                                                                id="addnewLine" 
                                                                @click="onAddNewRow"
                                                                :title="$t('AddNewRow')"
                                                            >
                                                                <em class="fa fa-plus pr-2"></em>{{ $t('AddNewRow') }}
                                                            </a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                
                                <!-- Comment and Adjustments -->
                                <div class="row">
                                    <div class="col-sm-12 col-lg-6">
                                        <div class="form-group h-100" id="CommentDiv">
                                            <label>{{ $t('Comment') }}:</label>
                                            <textarea 
                                                class="form-control" 
                                                id="Comment" 
                                                maxlength="10000" 
                                                name="Comment" 
                                                :placeholder="$t('Comment')" 
                                                rows="2" 
                                                style="height:100% !important;"
                                                v-model="invoiceData.Comment"
                                            ></textarea>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-lg-6">
                                        <!-- Discounts Table -->
                                        <div class="listing">
                                            <div class="table-responsive position-relative overflow-visible">
                                                <table class="table table-bordered" id="billAdjustment">
                                                    <thead class="thead-bg">
                                                        <tr role="row">
                                                            <th colspan="3">
                                                                <label class="pr-3">{{ $t('Discount') }}:</label>
                                                                <input 
                                                                    type="radio" 
                                                                    :data-resource="$t('IsSequential')" 
                                                                    checked="checked" 
                                                                    name="DiscountType" 
                                                                    value="Sequential"
                                                                    v-model="invoiceData.DiscountType"
                                                                    @change="calculateDiscountAndTax"
                                                                />
                                                                <input 
                                                                    type="radio" 
                                                                    :data-resource="$t('IsStacked')" 
                                                                    name="DiscountType" 
                                                                    value="Stack"
                                                                    v-model="invoiceData.DiscountType"
                                                                    @change="calculateDiscountAndTax"
                                                                />
                                                            </th>
                                                            <th>
                                                                <a 
                                                                    href="javascript:;" 
                                                                    id="ancDiscount" 
                                                                    :title="$t('AddMore')" 
                                                                    class="btn btn-primary float-right addDiscount mnw-90"
                                                                    @click="onAddDiscountRow"
                                                                >
                                                                    <em class="fa fa-plus pr-2" aria-hidden="true"></em>{{ $t('Add') }}
                                                                </a>
                                                            </th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr 
                                                            v-for="(discount, index) in discountRows" 
                                                            :key="index"
                                                            v-show="index > 0 || discountRows.length > 1"
                                                        >
                                                            <td width="38%">
                                                                <input 
                                                                    type="text" 
                                                                    v-model="discount.label" 
                                                                    name="discountLabel" 
                                                                    valfor="AdjName"
                                                                    maxlength="50" 
                                                                    class="form-control" 
                                                                />
                                                            </td>
                                                            <td width="30%">
                                                                <div class="input-group">
                                                                    <input 
                                                                        type="text" 
                                                                        v-model="discount.percentage" 
                                                                        name="discountPer" 
                                                                        valfor="AdjPercentage"
                                                                        value="0" 
                                                                        class="form-control txtPerDiscount"
                                                                        @blur="calculateDiscountAndTax"
                                                                        @keyup="checkDecimal"
                                                                    />
                                                                    <div class="input-group-append">
                                                                        <span class="input-group-text">%</span>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                            <td width="20%">
                                                                <input 
                                                                    type="hidden" 
                                                                    name="operator" 
                                                                    valfor="AdjOperator" 
                                                                    value="-" 
                                                                    attroper="0" 
                                                                />
                                                                <input 
                                                                    type="text" 
                                                                    name="discountTotal" 
                                                                    :value="discount.amount" 
                                                                    readonly="readonly" 
                                                                    valfor="AdjAmount" 
                                                                    class="form-control" 
                                                                />
                                                            </td>
                                                            <td width="8%" class="single-action text-center">
                                                                <a 
                                                                    href="javascript:;" 
                                                                    id="ancDiscountDel" 
                                                                    class="addDiscount aDelDiscount" 
                                                                    :title="$t('Delete')"
                                                                    @click="removeDiscountRow(index)"
                                                                >
                                                                    <em class="fa fa-trash text-danger" aria-hidden="true"></em>
                                                                </a>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        
                                        <!-- Credit Section -->
                                        <div class="listing mt-2">
                                            <div class="table-responsive">
                                                <table class="table table-bordered">
                                                    <thead class="thead-bg">
                                                        <tr>
                                                            <th colspan="3">
                                                                <label class="pr-3 float-left">{{ $t('Credit') }}:</label>
                                                                <div class="form-group mb-0 d-inline-block">
                                                                    <label class="switch">
                                                                        <input 
                                                                            type="checkbox" 
                                                                            value="1" 
                                                                            name="IsCredit" 
                                                                            class="dynamic custom-switch IsCredit"
                                                                            v-model="invoiceData.IsCredit"
                                                                            @change="onCreditToggle"
                                                                        />
                                                                        <span class="slider round">
                                                                            <span class="slider-yes text-left">{{ $t('Yes') }}</span>
                                                                            <span class="slider-no text-right pr-1">{{ $t('No') }}</span>
                                                                        </span>
                                                                    </label>
                                                                </div>
                                                            </th>
                                                            <th>&nbsp;</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr 
                                                            v-if="invoiceData.IsCredit"
                                                            class="ui-state-default" 
                                                            id="trBillCreditAmountRow"
                                                        >
                                                            <td width="38%">
                                                                {{ $t('TotalAvailableCredit') }} ({{ currency }}): 
                                                                <span id="spnCreditAmountBalance">{{ formatCurrency(creditAmountBalance) }}</span>
                                                            </td>
                                                            <td width="20%"></td>
                                                            <td width="20%">
                                                                <input 
                                                                    type="text" 
                                                                    name="txtCreditAmount" 
                                                                    id="txtCreditAmount" 
                                                                    class="form-control" 
                                                                    autocomplete="off"
                                                                    v-model="invoiceData.CreditAmount"
                                                                />
                                                            </td>
                                                            <td class="single-action" width="8%">
                                                                <a 
                                                                    href="javascript:;" 
                                                                    id="ancCreditDel" 
                                                                    class="addDiscount aCreditDiscount" 
                                                                    :title="$t('Delete')"
                                                                    @click="onRemoveCredit"
                                                                >
                                                                    <em class="fa fa-trash text-danger" aria-hidden="true"></em>
                                                                </a>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                                        
                                        <!-- Taxes Table -->
                                        <div class="listing mt-2">
                                            <div class="table-responsive">
                                                <table id="taxAdjustments" class="table table-bordered">
                                                    <thead class="thead-bg">
                                                        <tr>
                                                            <th colspan="3">{{ $t('Taxes') }}:</th>
                                                            <th>
                                                                <a 
                                                                    href="javascript:;" 
                                                                    id="ancTax" 
                                                                    :title="$t('AddMore')" 
                                                                    class="btn btn-primary float-right addDiscount mnw-90"
                                                                    @click="onAddTaxRow"
                                                                >
                                                                    <em class="fa fa-plus pr-2" aria-hidden="true"></em>{{ $t('Add') }}
                                                                </a>
                                                            </th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr 
                                                            v-for="(tax, index) in taxRows" 
                                                            :key="index"
                                                            v-show="index > 0 || taxRows.length > 1"
                                                            class="ui-state-default"
                                                        >
                                                            <td width="38%">
                                                                <input 
                                                                    type="text" 
                                                                    id="Tax2Label" 
                                                                    valfor="AdjName" 
                                                                    name="Tax2Label" 
                                                                    maxlength="50" 
                                                                    class="form-control"
                                                                    v-model="tax.label"
                                                                />
                                                            </td>
                                                            <td width="30%">
                                                                <div class="input-group">
                                                                    <input 
                                                                        type="text" 
                                                                        id="Tax2" 
                                                                        name="Tax2" 
                                                                        valfor="AdjPercentage"
                                                                        maxlength="5" 
                                                                        class="form-control txtTax"
                                                                        v-model="tax.percentage"
                                                                        @blur="calculateDiscountAndTax"
                                                                        @keyup="checkDecimal"
                                                                    />
                                                                    <div class="input-group-append">
                                                                        <span class="input-group-text">%</span>
                                                                    </div>
                                                                </div>
                                                            </td>
                                                            <td width="20%">
                                                                <input 
                                                                    type="hidden" 
                                                                    name="operator" 
                                                                    valfor="AdjOperator" 
                                                                    value="+" 
                                                                    attroper="1" 
                                                                />
                                                                <input
                                                                    type="text"
                                                                    name="Tax2Total"
                                                                    :value="tax.amount"
                                                                    readonly="readonly"
                                                                    valfor="AdjAmount"
                                                                    class="form-control"
                                                                />
                                                            </td>
                                                            <td class="single-action text-center" width="8%">
                                                                <a 
                                                                    href="javascript:;" 
                                                                    id="ancTaxDel" 
                                                                    class="addDiscount aDelDiscount" 
                                                                    :title="$t('Delete')"
                                                                    @click="removeTaxRow(index)"
                                                                >
                                                                    <em class="fa fa-trash text-danger" aria-hidden="true"></em>
                                                                </a>
                                                            </td>
                                                        </tr>
                                                    </tbody>
                                                    <tfoot>
                                                        <tr>
                                                            <td colspan="2" class="text-right">
                                                                <strong>{{ $t('GrandTotal') }} {{ currency }}: </strong>
                                                            </td>
                                                            <td>
                                                                <input 
                                                                    type="text" 
                                                                    v-model="invoiceData.BillTotalNoAdv" 
                                                                    class="form-control number" 
                                                                    style="pointer-events:none;" 
                                                                />
                                                            </td>
                                                            <td></td>
                                                        </tr>
                                                        <tr>
                                                            <td colspan="2" class="text-right">
                                                                <strong>{{ $t('DiscountFlatRate') }} {{ currency }}: </strong>
                                                            </td>
                                                            <td>
                                                                <input 
                                                                    type="text" 
                                                                    v-model="invoiceData.DiscountFlatRate" 
                                                                    class="form-control number" 
                                                                    maxlength="12"
                                                                    @change="calculateGrandTotal"
                                                                />
                                                            </td>
                                                            <td></td>
                                                        </tr>
                                                        <tr>
                                                            <td colspan="2" class="text-right">
                                                                <strong>{{ $t('AdvanceReceived') }} {{ currency }}: </strong>
                                                            </td>
                                                            <td>
                                                                <input 
                                                                    type="text" 
                                                                    v-model="invoiceData.BillAdvanceAmount" 
                                                                    class="form-control number" 
                                                                    maxlength="12"
                                                                    @change="calculateGrandTotal"
                                                                />
                                                            </td>
                                                            <td></td>
                                                        </tr>
                                                        <tr>
                                                            <td colspan="2" class="text-right">
                                                                <strong>{{ $t('NetTotal') }} {{ currency }}: </strong>
                                                            </td>
                                                            <td>
                                                                <input 
                                                                    type="text" 
                                                                    v-model="invoiceData.BillNetTotal" 
                                                                    class="form-control number" 
                                                                    style="pointer-events:none;" 
                                                                />
                                                            </td>
                                                            <td>
                                                                <input type="hidden" :value="invoiceData.StatusCode" id="invStatusCode" />
                                                            </td>
                                                        </tr>
                                                    </tfoot>
                                                </table>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        
                        <!-- Save/Cancel Buttons -->
                        <div class="row flex-row-reverse mt-2">
                            <div class="col-lg-6 text-right">
                                <a 
                                    href="javascript:;" 
                                    class="btn btn-success mr-2" 
                                    id="aSave" 
                                    name="aSave" 
                                     @click="onSave"
                                    :title="$t('Save')"
                                >
                                    <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                                </a>
                                <a 
                                    class="btn btn-danger" 
                                    :href="backUrl" 
                                    id="btnCancelColumnItems" 
                                    :title="$t('Cancel')"
                                >
                                    <em class="fa fa-close pr-2"></em>{{ $t('Cancel') }}
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
                                    <span class="mandatory">{{ $t('MandatoryString') }}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </div>
        
        <!-- Popup Components -->
        <EditInvoiceLineItem 
            v-if="showEditLineItem"
            :lineItemId="selectedLineItemId"
            :columnId="selectedColumnId"
            :ticketType="selectedTicketType"
            :isTicketManual="selectedIsTicketManual"
            :batchId="batchId"
            :clientId="invoiceData.ClientId"
            :divisionId="invoiceData.DivisionId"
            :dateOfInvoice="invoiceData.DateRangeFrom"
            :lineBatchId="lineBatchId"
            :billId="billId"
            :isAdd="isAddNewLineItem"
            @close="closeEditLineItem"
            @saved="onLineItemSaved"
        />
        
        <PartialNotesList 
            v-if="showNotesList"
            ref="notesListComponent"
            :key="refreshKey"
            :divisionId="selectedDivisionId"
            :utilityId="selectedUtilityId"
            :clientId="invoiceData.ClientId"
            :uniqueName="selectedUniqueName"
            @close="closeNotesList"
            @add-note="onAddNote"
            @edit-note="onAddNote"
            @note-deleted="onNoteDeleted"
        />
        
        <AddNotes 
            v-if="showAddNote"
            :noteId="selectedNoteId"
            :divisionId="selectedDivisionId"
            :utilityId="selectedUtilityId"
            :clientId="invoiceData.ClientId"
            :uniqueName="selectedUniqueName"
            @close="closeAddNote"
            @saved="onNoteSaved"
        />
        
        <AdvanceFilter 
            v-if="showAdvanceFilter"
            :setupId="setupId"
            :templateId="invoiceData.TemplateId"
            :filterFields="filterFields"
            @close="closeAdvanceFilter"
            @apply="onAdvanceFilterApply"
        />
        
        <ChangeClientAndUtility 
            v-if="showChangeContract"
            :lineItemIds="selectedLineItemIds"
            :oldClientId="invoiceData.ClientId"
            :oldUtilityName="oldUtilityName"
            :workDateFrom="invoiceData.DateRangeFrom"
            :workDateTo="invoiceData.DateRangeTo"
            :requestFrom="'lineitem'"
            :allInvoice="selectAllInvoice"
            :xmlData="filterXmlData"
            @close="closeChangeContract"
            @saved="onContractChanged"
        />
        
        <ManageWorkCode 
            v-if="showManageWorkCode"
            :lineItemIds="selectedLineItemIds"
            :requestFrom="'lineitem'"
            :allInvoice="selectAllInvoice"
            :xmlData="filterXmlData"
            :lineBatchId="lineBatchId"
            :invoiceBillId="billId"
            @close="closeManageWorkCode"
            @saved="onWorkCodeChanged"
        />
        
        <AddDiscount 
            v-if="showAddDiscount"
            :itemId="selectedDiscountItemId"
            :itemCost="selectedDiscountItemCost"
            @saving="onDiscountSaving"
            @saved="onDiscountSaved"
            @saved-complete="onDiscountSavedComplete"
            @cancel="closeAddDiscount"
        />
    </section>
</template>

<script>
import DataService from '../../services/DataService';
import {locateBillingService } from '../../services/locateBillingService';
import LineItems from './components/LineItems.vue';
import EditInvoiceLineItem from './components/EditInvoiceLineItem.vue';
import PartialNotesList from './components/PartialNotesList.vue';
import AddNotes from './components/AddNotes.vue';
import AdvanceFilter from './components/AdvanceFilter.vue';
import ChangeClientAndUtility from './components/ChangeClientAndUtility.vue';
import ManageWorkCode from './components/ManageWorkCode.vue';
import AddDiscount from './components/AddDiscount.vue';
import SearchableSelect from '../../components/Common/SearchableSelect.vue';
import Pager from '../../components/Common/Pager.vue';


export default {
    name: "InvoiceAdd",
    components: {
        LineItems,
        EditInvoiceLineItem,
        PartialNotesList,
        AddNotes,
        AdvanceFilter,
        ChangeClientAndUtility,
        ManageWorkCode,
        AddDiscount,
        SearchableSelect,
        Pager
    },
    data() {
        return {
            isLoading: false,
            billId: 0,
            UserId: '',
            batchId: null,
            lineBatchId: null,
            refreshKey: 0,
            setupId: 32,
            showMainForm: false,
            invoiceStatus: 'NotShowNumber',
            selectAllButtonState: '1',
            showActionMenu: false,
            pageTitle: '',
            currentCompanyId: null,
            currency: 'USD',
            
            // Search Data
            searchData: {
                ClientId: null,
                DivisionId: '',
                dateFrom: '',
                dateTo: '',
                NoOfNotes: 0
            },

            // Validation errors
            validationErrors: {
                ClientId: '',
                DivisionId: '',
                dateFrom: '',
                dateTo: ''
            },
            
            // Invoice Data
            invoiceData: {
                BillId: 0,
                ClientId: null,
                ClientName: '',
                BillNumber: null,
                LatestInvoiceNo: null,
                InvoiceStartNo: null,
                ParentCompanyId: '',
                BillIssueDate: '',
                DateRangeFrom: '',
                DateRangeTo: '',
                BillDueDateFrom: '',
                BillDueDateTo: '',
                Terms: '',
                BillDateComments: '',
                InvoiceMemo: '',
                InvoicePONumber: '',
                PaymentTermId: null,
                DivisionId: null,
                DivisionName: '',
                ProjectId: null,
                TemplateId: null,
                InvoiceTypeId: null,
                DetailFileFormat: null,
                SubTotal: 0,
