        onSearch() {
            var vm = this;
            vm.isLoading = true;
            vm.validationErrors = {
                ClientId: '',
                DivisionId: '',
                dateFrom: '',
                dateTo: ''
            };

            var hasError = false;
            
            // Validate Client
