        async showCompanyClientsDropDown(clientId) {

            var vm = this;
            vm.isLoading = true;
            
            var url = `clientId=${clientId}`;
          await  locateBillingService.GetCompanyByParentCompanyId(url).then((response) => {
                var data = response.data;
                var companyListData = [];
                
                // Get parent companies (those with empty FirstAttribute)
                var parentData = data.filter(function(val) {
                    return val.firstAttribute === "" || val.firstAttribute === null;
                });
                // Build hierarchical company list
                parentData.forEach(function(parent) {
                    // Add parent company
                    companyListData.push({
                        Value: parent.value,
                        Text: parent.text,
                        IsParent: true
                    });
                    // Get child companies for this parent
                    var childData = data.filter(function(val) {
                        return val.firstAttribute === parent.value;
                    });
                    // Add child companies
                    childData.forEach(function(child) {
                        companyListData.push({
                            Value: child.value,
                            Text: '  ' + child.text, // Indent child items
                            IsParent: false,
                            ParentId: parent.value
                        });
                    });
                });
                
                vm.companyList = companyListData;
                vm.isLoading = false;
            }).catch((error) => {
                console.log('Error loading company clients:', error);
                vm.companyList = [];
                vm.isLoading = false;
            });
