        loadLineItems() {
            var vm = this;
            vm.isLoading = true;

            // Validate required parameters
            var clientId = vm.invoiceData.ClientId || vm.searchData.ClientId;
            if (!clientId || clientId === 0 || clientId === '0' || clientId === '') {
                console.warn('ClientId is required for loading line items');
                vm.isLoading = false;
                return;
            }
            
            if (!vm.setupId || vm.setupId === 0) {
                console.warn('SetupId is required for loading line items');
                vm.isLoading = false;
                return;
            }

            // Build filter XML from active filters if they exist
            if (vm.activeFilters && vm.activeFilters.length > 0) {
                vm.buildFilterXml();
            } else {
                vm.filterXmlData = '';
            }

            // Build query parameters for FetchLineItems API
            var params = [];
            
            // Required and optional parameters
            if (vm.billId) params.push(`billId=${vm.billId}`);
            
            // Required: clientId and setupId
            params.push(`clientId=${clientId}`);
            params.push(`setupId=${vm.setupId}`);
            
            var divisionId = vm.invoiceData.DivisionId || vm.searchData.DivisionId;
            if (divisionId) params.push(`divisionId=${divisionId}`);
            
            if (vm.invoiceData.TemplateId) params.push(`templateId=${vm.invoiceData.TemplateId}`);
            
            // Use DateRangeFrom/DateRangeTo if available, otherwise use search dates
            // Format dates as YYYY-MM-DD for API
            var dateFrom = vm.invoiceData.DateRangeFrom || vm.searchData.dateFrom;
            var dateTo = vm.invoiceData.DateRangeTo || vm.searchData.dateTo;
            
            if (dateFrom) {
                var dateFromFormatted = vm.formatDateForAPI(dateFrom);
                if (dateFromFormatted) {
                    params.push(`dateFrom=${encodeURIComponent(dateFromFormatted)}`);
                } else {
                    console.warn('Invalid dateFrom format:', dateFrom);
                }
            }
            
            if (dateTo) {
                var dateToFormatted = vm.formatDateForAPI(dateTo);
                if (dateToFormatted) {
                    params.push(`dateTo=${encodeURIComponent(dateToFormatted)}`);
                } else {
                    console.warn('Invalid dateTo format:', dateTo);
                }
            }
            
            // Optional parameters with defaults - use paging
            var offset = ((vm.currentPage - 1) * vm.pageSize) + 1;
            params.push(`offset=${offset}`);
            params.push(`fetchNext=${vm.pageSize}`);
            
            console.log('Loading line items - Page:', vm.currentPage, 'PageSize:', vm.pageSize, 'Offset:', offset, 'FetchNext:', vm.pageSize);
            if (vm.batchId) params.push(`batchId=${vm.batchId}`);
            params.push(`requestFrom=INVOICEEDITMODE`);
            if (vm.lineBatchId) params.push(`lineBatchId=${vm.lineBatchId}`);
            params.push(`sortBy=${vm.sortBy || '1'}`);
            params.push(`sortExp=${vm.sortExp || 'Asc'}`);
            
            // Optional parameters (only include if they have values)
            if (vm.filterXmlData) params.push(`xmlData=${encodeURIComponent(vm.filterXmlData)}`);
            
            var url = params.join('&');
            
            console.log('Loading line items with URL:', url);
            
            return locateBillingService.LoadLineItems(url).then((response) => {
                console.log('Line items response:', response);
                
                if (response && response.data) {
                    var data = response.data;
                    
                    // Handle response structure - API returns camelCase: lineItems, totalRecords, etc.
                    if (data.lineItems || data.LineItems) {
                        // Use camelCase first, fallback to PascalCase for compatibility
                        var lineItems = data.lineItems || data.LineItems || [];
                        
                        console.log('Received line items:', lineItems.length, 'items for page', vm.currentPage);
                        
                        // Use Vue.set for reactivity
                        vm.$set(vm, 'lineItemsData', lineItems);

                        // Add selected property for checkboxes
                        vm.lineItemsData.forEach(item => {
                            vm.$set(item, 'selected', false);
                        });
                        
                        // Extract headers from first line item if available
                        if (vm.lineItemsData.length > 0) {
                            var firstItem = vm.lineItemsData[0];
                            var headerNames = firstItem.headerNames || firstItem.HeaderNames;
                            
                            if (headerNames) {
                                try {
                                    // Parse header names string (format: "  request no -S-TicketNumber-N, type -S-tickettype-N, ...")
                                    // Split by comma to get individual headers
                                    var headersArray = headerNames.split(',');
                                    var parsedHeaders = [];
                                    
                                    headersArray.forEach(function(headerStr) {
                                        var trimmed = headerStr.trim();
                                        if (trimmed) {
                                            // Split by dash: [0] = display name, [1] = data type, [2] = column name, [3] = format (optional)
                                            var parts = trimmed.split('-');
                                            if (parts.length >= 2) {
                                                parsedHeaders.push({
                                                    displayName: parts[0].trim(),
                                                    dataType: parts[1].trim(),
                                                    columnName: parts.length > 2 ? parts[2].trim() : '',
                                                    format: parts.length > 3 ? parts[3].trim() : 'N',
                                                    fullString: trimmed
                                                });
                                            }
                                        }
                                    });
                                    
                                    vm.$set(vm, 'lineItemHeaders', parsedHeaders);
                                    console.log('Parsed headers:', parsedHeaders);
                                } catch (e) {
                                    console.warn('Error parsing headers:', e);
                                    vm.$set(vm, 'lineItemHeaders', []);
                                }
                            }
                        }
                        
                        // Calculate subtotal from line items
                        var subTotal = 0;
                        vm.lineItemsData.forEach(function(item) {
                            // Handle both camelCase and PascalCase
                            var itemSubTotal = item.subTotal || item.SubTotal || 0;
                            subTotal += parseFloat(itemSubTotal) || 0;
                        });
                        vm.$set(vm.invoiceData, 'SubTotal', subTotal);
                        
                        // Store batch ID if available
                        if (data.batchId || data.BatchId) {
                            vm.batchId = data.batchId || data.BatchId;
                        }
                        
                        // Store credit amount balance
                        if (data.creditAmountBalance !== undefined || data.CreditAmountBalance !== undefined) {
                            vm.creditAmountBalance = data.creditAmountBalance !== undefined ? data.creditAmountBalance : data.CreditAmountBalance;
                        }
                        
                        // Store not in summary cost
                        if (data.notInSummaryCost !== undefined || data.NotInSummaryCost !== undefined) {
                            vm.notInSummaryCost = data.notInSummaryCost !== undefined ? data.notInSummaryCost : data.NotInSummaryCost;
                        }
                        
                        // Set template ID from response if available and not already set
                        var templateId = data.clientInvoiceTemplateId || data.ClientInvoiceTemplateId;
                        if (templateId && !vm.invoiceData.TemplateId) {
                            vm.$set(vm.invoiceData, 'TemplateId', templateId);
                        }
                        
                        // Set payment due date if available
                        if (data.paymentDueDate || data.PaymentDueDate) {
                            vm.$set(vm.invoiceData, 'PaymentDueDate', data.paymentDueDate || data.PaymentDueDate);
                        }
                        
                        // Load filter fields after line items are loaded (only if template is available)
                        if (vm.invoiceData.TemplateId) {
                            // Use $nextTick to ensure template is set before loading fields
                            vm.$nextTick(function() {
                                vm.loadFilterFields();
                            });
                        }
                        
                        // Create pager info from totalRecords
                        var totalRecords = data.totalRecords || data.TotalRecords;
                        if (totalRecords !== undefined) {
                            vm.totalRecords = totalRecords;
                            vm.$set(vm, 'lineItemsPager', {
                                TotalRecords: totalRecords,
                                CurrentPage: vm.currentPage,
                                PageSize: vm.pageSize,
                                TotalPages: Math.ceil(totalRecords / vm.pageSize)
                            });
                            
                            // Update Pager component if it exists
                            vm.$nextTick(function() {
                                if (vm.$refs.lineItemsPagerComponent) {
                                    vm.$refs.lineItemsPagerComponent.PageNumber = vm.currentPage;
                                    vm.$refs.lineItemsPagerComponent.PageSize = vm.pageSize;
                                }
                            });
                        }
                    } else if (data.Records) {
                        // Fallback for different response structure
                        vm.$set(vm, 'lineItemsData', data.Records || []);
                        vm.$set(vm, 'lineItemHeaders', data.Headers || []);
                        vm.$set(vm, 'lineItemsPager', data.Pager);
                        vm.$set(vm.invoiceData, 'SubTotal', data.SubTotal || 0);
                    } else {
                        console.warn('Unexpected response structure:', data);
                        vm.$set(vm, 'lineItemsData', []);
                    }
                } else {
                    console.warn('No data in response:', response);
                    vm.$set(vm, 'lineItemsData', []);
                }
                vm.isLoading = false;
            }).catch((error) => {
                vm.isLoading = false;
                console.log('Error loading line items:', error);
                vm.$set(vm, 'lineItemsData', []);
                
                // Show user-friendly error message
                var errorMessage = 'Error loading line items.';
                if (error.response) {
                    console.log('Error response:', error.response.data);
                    if (error.response.data && error.response.data.message) {
                        errorMessage = error.response.data.message;
                    } else if (error.response.status) {
                        errorMessage = 'Error ' + error.response.status + ': ' + (error.response.statusText || 'Request failed');
                    }
                } else if (error.message) {
                    errorMessage = error.message;
                }
                // You can show a toast/alert here if needed
                console.log(errorMessage);
                console.log('Full error details:', error);
            });
