        onApplyFilter() {
            var vm = this;
            
            // Validation: columnId is required
            if (!vm.filterData.columnId || vm.filterData.columnId === '' || vm.filterData.columnId === null) {
                console.warn('Please select a column to filter');
                return;
            }
            
            // For special fields (-1, -2), columnValue is optional, but for others it's required
            if (vm.filterData.columnId !== '-1' && vm.filterData.columnId !== '-2') {
                if (!vm.filterData.columnValue || vm.filterData.columnValue.trim() === '') {
                    console.warn('Please enter a filter value');
                    return;
                }
            }
            
            // Add to active filters
            var selectedField = vm.filterFields.find(f => String(f.TEMPLATE_ID) === String(vm.filterData.columnId));
            if (selectedField) {
                // Check if filter already exists
                var existingFilter = vm.activeFilters.find(f => 
                    String(f.fieldId) === String(vm.filterData.columnId) && 
                    String(f.fieldValue) === String(vm.filterData.columnValue)
                );
                
                if (!existingFilter) {
                vm.activeFilters.push({
                    fieldId: vm.filterData.columnId,
                    fieldType: selectedField.DATA_TYPE,
                    tablename: selectedField.TABLE_NAME,
                    columnname: selectedField.COLUMN_NAME,
                        fieldValue: vm.filterData.columnValue || '',
                    displayName: selectedField.DISPLAY_NAME
                });
                }
            }
            
            // Reset filter inputs
            vm.filterData.columnId = '';
            vm.filterData.columnValue = '';
            
            // Reset to first page when applying filter
            vm.currentPage = 1;
            
            // Build filter XML and reload line items
            vm.buildFilterXml();
            vm.loadLineItems();
        },
        removeFilter(index) {
            var vm = this;
            vm.activeFilters.splice(index, 1);
            vm.currentPage = 1;
            vm.buildFilterXml();
            // Reset pager component if it exists
            if (vm.$refs.lineItemsPagerComponent) {
                vm.$refs.lineItemsPagerComponent.PageNumber = 1;
                vm.$refs.lineItemsPagerComponent.CurrentPage = 1;
