        onAdvanceFilterApply(filterData) {
            var vm = this;
            vm.closeAdvanceFilter();
            
            // Convert advance filter conditions to active filters
            if (filterData && filterData.conditions) {
                filterData.conditions.forEach(function(condition) {
                    if (condition.templateId && condition.operatorId) {
                        var field = vm.filterFields.find(f => String(f.TEMPLATE_ID) === String(condition.templateId));
                        if (field) {
                            var operator = null;
                            // Find operator from the condition's operator list if available
                            // For now, use the operatorId to build filter
                            
                            var filterValue = condition.fieldFirstValue || '';
                            if (condition.fieldSecondValue) {
                                filterValue = condition.fieldFirstValue + ' AND ' + condition.fieldSecondValue;
                            }
                            
                            // Add to active filters
                            vm.activeFilters.push({
                                fieldId: condition.templateId,
                                fieldType: field.DATA_TYPE,
                                tablename: field.TABLE_NAME,
                                columnname: field.COLUMN_NAME,
                                fieldValue: filterValue,
                                displayName: field.DISPLAY_NAME,
                                operatorId: condition.operatorId,
                                relationWithParent: condition.relationWithParent,
                                openingBrackets: condition.openingBrackets,
                                closingBrackets: condition.closingBrackets,
                                isAdvanceFilter: true
                            });
                        }
                    }
                });
            }
            
            // Reset to first page when applying filter
            vm.currentPage = 1;
            
            // Build filter XML and reload line items
            vm.buildFilterXml();
            vm.loadLineItems();
