        buildFilterXml() {
            var vm = this;
            if (!vm.activeFilters || vm.activeFilters.length === 0) {
                vm.filterXmlData = '';
                return;
            }
            
            // Build XML structure matching MVC format: <Fields><Field>...</Field></Fields>
            var xmlParts = [];
            xmlParts.push('<Fields>');
            
            vm.activeFilters.forEach(function(filter) {
                xmlParts.push('\n<Field>');
                xmlParts.push('\n\t<FieldId>' + (filter.fieldId || '') + '</FieldId>');
                xmlParts.push('\n\t<FieldValue>' + (filter.fieldValue || '') + '</FieldValue>');
                xmlParts.push('\n\t<FieldColumn>' + (filter.columnname || '') + '</FieldColumn>');
                xmlParts.push('\n</Field>');
            });
            
            xmlParts.push('\n</Fields>');
            xmlParts.push('\n');
            vm.filterXmlData = xmlParts.join('');
        },
