                    });
                    
                    // Add fields from API response
                    if (fields.length > 0) {
                        fields.forEach(function(field) {
                            // Handle both dynamic object and regular object
                            var templateId = field.TEMPLATE_ID || field.templateId || field.TemplateId || field.Template_ID;
                            var displayName = field.DISPLAY_NAME || field.displayName || field.DisplayName || field.DISPLAYNAME;
                            var dataType = field.DATA_TYPE || field.dataType || field.DataType || field.DATATYPE;
                            var columnName = field.COLUMN_NAME || field.columnName || field.ColumnName || field.COLUMNNAME;
                            var tableName = field.TABLE_NAME || field.tableName || field.TableName || field.TABLENAME;
                            
                            // Ensure field has required properties
                            if (templateId !== undefined && displayName) {
                                vm.filterFields.push({
                                    DISPLAY_NAME: displayName,
                                    TEMPLATE_ID: String(templateId),
