        getColumnWidth() {
            // Calculate column width similar to CSHTML: 90M / headersArray.Count()
            var headerCount = this.lineItemHeaders ? this.lineItemHeaders.length : 1;
            if (headerCount === 0) return 10;
            return Math.floor(90 / headerCount);
        },
        getHeaderClass(index) {
            // Similar to CSHTML: if (i > ColumnCount) class="hideColumnLineItem"
            var columnCount = this.lineItemHeaders ? Math.floor(this.lineItemHeaders.length / 2) : 8;
            if (columnCount < 9) columnCount = 8;
            return index > columnCount ? 'hideColumnLineItem' : '';
        },
        getRowClass(item) {
            // Similar to CSHTML row class logic
            var rowClass = '';
            if (item.isManual === true || item.IsManual === true) {
                rowClass = 'low-bar';
            }
            if ((item.isNonVisibleInSummary === true || item.IsNonVisibleInSummary === true) && 
                !(item.isManual === true || item.IsManual === true)) {
                rowClass = 'high-bar';
            }
            if ((item.isManual === true || item.IsManual === true) && 
                (item.isNonVisibleInSummary === true || item.IsNonVisibleInSummary === true)) {
                rowClass = 'critical-bar';
            }
            return rowClass;
        },
        getCellValue(item, colIndex) {
            // Get value from col1, col2, etc. based on index
            var colName = 'col' + (colIndex + 1);
            var ColName = 'Col' + (colIndex + 1);
            return item[colName] || item[ColName] || '';
        },
        getCellClass(header, colIndex) {
            var cssClass = '';
            // Add txtUTotal class for cost column (col8, index 7)
            if (colIndex === 7) {
                cssClass += 'txtUTotal ';
            }
            // Add textalignright for numeric types
            if (header.dataType === 'D' || header.dataType === 'I' || header.dataType === 'Si') {
                cssClass += 'textalignright';
            }
            // Add hideColumnLineItem if beyond column count
            var columnCount = this.lineItemHeaders ? Math.floor(this.lineItemHeaders.length / 2) : 8;
            if (columnCount < 9) columnCount = 8;
            if (colIndex > columnCount) {
                cssClass += ' hideColumnLineItem';
            }
            return cssClass.trim();
        },
        formatCellValue(item, colIndex, header) {
            var txtValue = this.getCellValue(item, colIndex);
            if (!txtValue) return '';
            
            var dataType = header.dataType || '';
            var format = header.format || 'N';
            
            // Format based on data type (similar to CSHTML)
            if (dataType === 'DT' && txtValue) {
                // DateTime formatting
                try {
                    var date = new Date(txtValue);
                    if (!isNaN(date.getTime())) {
                        if (format && format !== 'N') {
                            // Use custom format if provided
                            return this.formatDate(date, format);
                        } else {
                            // Default: MM/dd/yyyy HH:mm
                            return this.formatDate(date, 'MM/dd/yyyy') + ' ' + this.formatTime(date);
                        }
                    }
                } catch (e) {
                    console.warn('Error formatting date:', e);
                }
            } else if (dataType === 'T' && txtValue) {
                // Time formatting
                try {
                    var date = new Date(txtValue);
                    if (!isNaN(date.getTime())) {
                        if (format && format !== 'N') {
                            return this.formatDate(date, format);
                        } else {
                            return this.formatTime(date);
                        }
                    }
                } catch (e) {
                    console.warn('Error formatting time:', e);
                }
            } else if (dataType === 'D' && txtValue) {
                // Decimal formatting
                try {
                    var num = parseFloat(txtValue);
                    if (!isNaN(num)) {
                        return num.toFixed(2);
                    }
                } catch (e) {
                    console.warn('Error formatting decimal:', e);
                }
            }
            
            // Special handling for cost column (col8, index 7)
            if (colIndex === 7 && header.displayName && header.displayName.toLowerCase().trim() === 'cost') {
                try {
                    var cost = parseFloat(txtValue);
                    if (!isNaN(cost)) {
                        return cost.toFixed(2);
                    }
                } catch (e) {
                    console.warn('Error formatting cost:', e);
                }
            }
            
            return txtValue;
        },
        formatDate(date, format) {
            // Simple date formatting - can be enhanced with a library like moment.js
            var month = (date.getMonth() + 1).toString().padStart(2, '0');
            var day = date.getDate().toString().padStart(2, '0');
            var year = date.getFullYear();
            
            if (format === 'MM/dd/yyyy') {
                return month + '/' + day + '/' + year;
            }
            // Add more format options as needed
            return month + '/' + day + '/' + year;
        },
        formatTime(date) {
            var hours = date.getHours();
            var minutes = date.getMinutes();
            var ampm = hours >= 12 ? 'PM' : 'AM';
            hours = hours % 12;
            hours = hours ? hours : 12;
            var minutesStr = minutes.toString().padStart(2, '0');
            return hours + ':' + minutesStr + ' ' + ampm;
        },
        getCostValue(item) {
            // Get cost value from col8
            return parseFloat(item.col8 || item.Col8 || 0);
        },
