        calculateGrandTotal() {
            var vm = this;
            var subtotal = parseFloat(vm.invoiceData.SubTotal) || 0;
            var discountTotal = vm.discountRows.reduce((sum, d) => sum + parseFloat(d.amount || 0), 0);
            var taxTotal = vm.taxRows.reduce((sum, t) => sum + parseFloat(t.amount || 0), 0);
            var credit = parseFloat(vm.invoiceData.CreditAmount) || 0;
            var flatDiscount = parseFloat(vm.invoiceData.DiscountFlatRate) || 0;
            var advance = parseFloat(vm.invoiceData.BillAdvanceAmount) || 0;

            vm.invoiceData.BillTotalNoAdv = (subtotal - discountTotal + taxTotal).toFixed(2);
            vm.invoiceData.BillNetTotal = (parseFloat(vm.invoiceData.BillTotalNoAdv) - flatDiscount - advance - credit).toFixed(2);
        },
