<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered modal-xl">
                    <div class="modal-content">
                        <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">
                                {{ isAdd ? $t('AddNewLineItem') : $t('EditLineItem') }}
                            </h5>
                            <button type="button" class="close" @click="onClose">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">
                            <!-- Ticket / Basic Info -->
                            <div class="row">
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('TicketNumber') }}</label>
                                        <input
                                            type="text"
                                            class="form-control"
                                            v-model="form.TicketNumber"
                                            :readonly="!isAdd"
                                        />
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('ActivityId') }} <span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.ActivityId"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in activityList"
                                                :key="item.Value"
                                                :value="item.Value"
                                            >
                                                {{ item.Name || item.Text }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('StatusCode') }} <span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.Status"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in statusList"
                                                :key="item.Value"
                                                :value="item.Value"
                                            >
                                                {{ item.Text }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                            </div>

                            <!-- Location / Dates -->
                            <div class="row">
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('WorkDate') }}</label>
                                        <input
                                            type="text"
                                            class="form-control hasdatetimepicker"
                                            v-model="form.WorkDate"
                                            id="WorkDate"
                                            autocomplete="off"
                                        />
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('TransmitDate') }}</label>
                                        <input
                                            type="text"
                                            class="form-control hasdatetimepicker"
                                            v-model="form.TransmitDate"
                                            id="TransmitDate"
                                            autocomplete="off"
                                        />
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('ParseDate') }}</label>
                                        <input
                                            type="text"
                                            class="form-control hasdatetimepicker"
                                            v-model="form.ParseDate"
                                            id="ParseDate"
                                            autocomplete="off"
                                        />
                                    </div>
                                </div>
                            </div>

                            <!-- Financials -->
                            <div class="row">
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('CostCenters') }} <span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.DistrictCode"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in costCenterList"
                                                :key="item.Value"
                                                :value="item.Value"
                                            >
                                                {{ item.Text }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('WorkCode') }} <span class="mandatory">*</span></label>
                                        <select
                                            class="form-control"
                                            v-model="form.WorkCodeId"
                                        >
                                            <option value="">{{ $t('Select') }}</option>
                                            <option
                                                v-for="item in workCodeList"
                                                :key="item.Value"
                                                :value="item.Value"
                                            >
                                                {{ item.Name || item.Text }}
                                            </option>
                                        </select>
                                    </div>
                                </div>
                                <div class="col-lg-4">
                                    <div class="form-group">
                                        <label>{{ $t('Rate') }}</label>
                                        <input
                                            type="text"
                                            class="form-control"
                                            v-model="form.UnitAmountTotal"
                                        />
                                    </div>
                                </div>
                            </div>

                            <!-- Comment -->
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{ $t('Comment') }}</label>
                                        <textarea
                                            class="form-control"
                                            v-model="form.Comment"
                                            maxlength="500"
                                            rows="3"
                                        ></textarea>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="modal-footer">
                            <button class="btn btn-success" @click="onSave">
                                <em class="fa fa-save pr-2" aria-hidden="true"></em>{{ $t('Save') }}
                            </button>
                            <button class="btn btn-danger" @click="onClose">
                                <em class="fa fa-close pr-2" aria-hidden="true"></em>{{ $t('Cancel') }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    name: 'EditInvoiceLineItem',
    props: {
        lineItemId: {
            type: [Number, String],
            default: null
        },
        columnId: {
            type: [Number, String],
            default: null
        },
        ticketType: {
            type: String,
            default: null
        },
        isTicketManual: {
            type: String,
            default: null
        },
        batchId: {
            type: [Number, String],
            default: null
        },
        clientId: {
            type: [Number, String],
            default: null
        },
        divisionId: {
            type: [Number, String],
            default: null
        },
        dateOfInvoice: {
            type: String,
            default: ''
        },
        lineBatchId: {
            type: String,
            default: ''
        },
        billId: {
            type: [Number, String],
            default: null
        },
        isAdd: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            isLoading: false,
            form: {
                TicketNumber: '',
                ActivityId: null,
                Description: '',
                Status: '',
                WorkDate: '',
                TransmitDate: '',
                ParseDate: '',
                DistrictCode: '',
                WorkCodeId: '',
                UnitAmountTotal: 0,
                Comment: ''
            },
            activityList: [],
            statusList: [],
            costCenterList: [],
            workCodeList: []
        };
    },
    created() {
        this.loadDropdowns();
        if (this.lineItemId) {
            this.loadLineItem();
        }
    },
    methods: {
        loadDropdowns() {
            // TODO: Replace with actual API calls for activities, status, cost centers, and work codes
        },
        loadLineItem() {
            this.isLoading = true;
            // TODO: Replace with actual API call to load line item details
            // Example:
            // DataService.GetInvoiceLineItem(`columnId=${this.columnId}&lineItemId=${this.lineItemId}&billId=${this.billId}`).then(...)
            setTimeout(() => {
                this.isLoading = false;
            }, 100);
        },
        onSave() {
            // Basic required validation (can be expanded)
            if (!this.form.ActivityId || !this.form.Status || !this.form.DistrictCode || !this.form.WorkCodeId) {
                // TODO: Hook into global alert / validation messaging
                return;
            }
            this.isLoading = true;
            // TODO: Replace with actual API call for save/update
            // DataService.UpdateInvoiceLineItem(this.form).then(...)
            setTimeout(() => {
                this.isLoading = false;
                this.$emit('saved');
            }, 200);
        },
        onClose() {
            this.$emit('close');
        }
    }
};
</script>

<style scoped>
.mandatory {
    color: red;
}
</style>


